import { Injectable } from '@angular/core';
import * as store from 'store';
import {UsefulDataService} from './useful-data.service';


@Injectable()
export class LocalDataService {
  private _data;

  constructor(private usefulData: UsefulDataService){

  }

  setDataModel(data) : void {
    let timestamp = Date.now();
    store.set('model', data);
    store.set('modelLastUpdate', timestamp);
  }

  getDataModel() {
    this._data = store.get('model');
    let lastUpdateDate = new Date(store.get('modelLastUpdate'));
    let nowDate = new Date(); 
    let minutesDiff = ( (nowDate.getTime() - lastUpdateDate.getTime()) / 1000 / 60 );

    if(minutesDiff < this.usefulData.getDataPersistenceTimeout()){
      return this._data;  
    } 
    return null;
  }

  getDataModelHolder() {
    this._data = store.get('modelHolder');
    let lastUpdateDate = new Date(store.get('modelHolderLastUpdate'));
    let nowDate = new Date(); 
    let minutesDiff = ( (nowDate.getTime() - lastUpdateDate.getTime()) / 1000 / 60 );

    if(minutesDiff < this.usefulData.getDataPersistenceTimeout()){
      return this._data;  
    } 
    return null;
  }

  setDataModelHolder(data) : void {
    let timestamp = Date.now();
    store.set('modelHolder', data);
    store.set('modelHolderLastUpdate', timestamp);
  }

  setDataLang(data) : void {
    let timestamp = Date.now();
    store.set('lang', data);
    store.set('langLastUpdate', timestamp); 
  }

  getDataLang() {
    this._data = store.get('lang');
    let lastUpdateDate = new Date(store.get('langLastUpdate'));
    let nowDate = new Date(); 
    let minutesDiff = ( (nowDate.getTime() - lastUpdateDate.getTime()) / 1000 / 60 );

    if(minutesDiff < this.usefulData.getDataPersistenceTimeout()){
      return this._data;  
    } 
    return null;
  }


}



