import {trigger, state, animate, style, transition} from '@angular/core';

export function routerTransition() {
  return slideToLeft();
}

export function holderTransition() {
  return flipToRight();
}

export function extendTransition() {
  return extend();
}

function extend(){
  return trigger('extendTransition', 
  [
    state('*', style({width:'100%',transform: 'scaleY(1)', position: 'static'}) ),
    state('void', style({width:'100%',transform: 'scaleY(0)', position: 'absolute'}) ),
    transition(
        ":enter", // ---> Entering --->
        [
          style({transform: 'scaleY(0)' }),
          animate(
              "300ms linear",
              style({
                  transform: 'scaleY(1)'
              })
          )
        ]
    )

  ])
}

function slideToLeft() {
  return trigger('routerTransition', 
  [
    transition(
        "void => prev", // ---> Entering --->
        [
          style({position:'absolute', left: '0px', top: '40px', width:'100%', transform: 'translateX(-100%)', opacity: 0}),
          animate(
              "700ms ease-in-out",
              style({
                  transform: 'translateX(0%)',
                  opacity: 1
              })
          )
        ]
    ),

    transition(
        "prev => void", // ---> Leaving --->
        [
          style({position:'absolute', left: '0px', top: '40px', width:'100%', transform: 'translateX(0%)', opacity: 1}),
          animate(
              "700ms ease-in-out",
              style({
                  transform: 'translateX(-100%)',
                  opacity: 0
              })
          )
        ]
    ),
    transition(
        "void => next", // <--- Entering <---
        [
          style({position:'absolute', left: '0px', top: '40px', width:'100%', transform: 'translateX(100%)', opacity: 0}),
          animate(
              "700ms ease-in-out",
              style({
                  transform: 'translateX(0%)',
                  opacity: 1
              })
          )
        ]
    ),
    transition(
        "next => void", // <--- Leaving <---
        [
          style({position:'absolute', left: '0px', top: '40px', width:'100%', transform: 'translateX(0%)', opacity: 1}),
          animate(
              "700ms ease-in-out",
              style({
                  transform: 'translateX(100%)',
                  opacity: 1
              })
          )
        ]
    )
]

  );
}


function flipToRight() {
  return trigger('holderTransition', 
  [
  state(':leave', style({display: 'none'})),
  transition(
        "* => leave", // ---> Entering --->
        [
          style({position:'absolute', opacity: 1 }),
          animate(
              "500ms ease-in-out",
              style({
                  transform: 'rotateY(-180deg)',
                  opacity: 0
              })
          )
        ]
    ),

  transition(
        "* => enter", // ---> Entering --->
        [
          style({position:'absolute', transform: 'rotateY(180deg)', opacity: 0 }),
          animate(
              "500ms ease-in-out",
              style({
                  transform: 'rotateY(0deg)',
                  opacity: 1
              })
          )
        ]
    )

  ])
}


