import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';

@Injectable()
export class HeaderStateService {
  private _steps : BehaviorSubject<HEADER_STATE>;
  private __currentStep = HEADER_STATE.FILL_DATA;
  constructor() {
    this._steps = new BehaviorSubject(this.__currentStep);
   }

   currentState(){
     return this._steps.asObservable()
   }
   setCurrentState(step: HEADER_STATE){
     this.__currentStep = step;
     this._steps.next(step);
   }
}

export enum HEADER_STATE{
  FILL_DATA = 0,
  E_SIGN_CONTARCT,
  UPLOAD_JUSTIFICATIFS,
  ACTIVATE_ACCOUNT
}


