import { Pipe, PipeTransform } from '@angular/core';
import * as moment from  'moment'
@Pipe({
  name: 'pibTime',
  pure: false
})
export class HsbcTimePipe implements PipeTransform {
  /**
   * transform a date string to other format switch language
   * @param value
   * @param args
   * @returns {string}
   */
  transform(value: string, args: string): any {
    let format = args == 'fr' ? 'DD/MM/YYYY' : 'MM/DD/YYYY'
    return moment(value,'MM/DD/YYYY').format(format)
  }
}


