import { Component, HostBinding, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Location } from '@angular/common';
import { FillDataStepsChangerService, FILL_DATA_STEPS } from '../../services/fill-data-steps-changer.service';
import { HeaderStateService, HEADER_STATE } from '../../services/header-state.service';
import { SharedModelService } from '../../services/shared-model.service';
import { ScrollService } from '../../services/scroll.service';
import { RouterStateService } from '../../services/router-state.service';
import { routerTransition, holderTransition } from '../../routing/router.animations';
@Component({
  selector: 'app-upload-proof-tax-notice',
  templateUrl: './upload-proof-tax-notice.component.html',
  styleUrls: ['./upload-proof-tax-notice.component.css'],
  animations: [routerTransition(), holderTransition()],
  host: {'[@routerTransition]': ''},
})
export class UploadProofTaxNoticeComponent implements OnInit {

  /**
   * @var {any} taxProof
   */
  taxProof: any;
  @HostBinding('@routerTransition') routerAnimation;

  /**
   * @constructor
   * Constructor of UploadProofTaxNoticeComponent class
   * @param {Location} _location location service
   * @param {Router} router router service
   * @param {HeaderStateService} _headerSteps
   * @param {SharedModelService} _sharedModelService Model service
   * @param {FillDataStepsChangerService} _fillDataStepService 
   */
  constructor(private _location: Location,
    private router: Router,
    private _headerSteps: HeaderStateService,
    private _sharedModelService: SharedModelService,
    private _fillDataStepService: FillDataStepsChangerService,
    private _routerState: RouterStateService,
    private _scroll: ScrollService) {
      this.routerAnimation = this._routerState.lastAction;
      setTimeout(() => {
        this.routerAnimation = 'next'
      }, 500);
    }

  /**
   * Initialisations
   * @function ngOnInit
   */
  ngOnInit() {
    this._fillDataStepService.setCurrentStep(-1);
    this._headerSteps.setCurrentState(HEADER_STATE.UPLOAD_JUSTIFICATIFS);
  }

  /**
   * Fired when select a file
   * @function onSelect
   * @param {any} event
   * @param {string} forFile
   */
  onSelect(event) {
    if (event.file) {
      this.taxProof = event.file;
    }
  }

  /**
   * Move to the previous step
   * @function goBack
   */
  goBack() {
    this._location.back();
  }

  /**
   * Move to the next step
   * @function next
   */
  next() {
    this.routerAnimation = 'prev';
    setTimeout(() => {
      this.router.navigate(['proof-upload-validation']);
    }, 50);
  }
}



