import { Component, HostBinding, OnInit } from '@angular/core';
import {Router}  from '@angular/router';
import {Location} from '@angular/common';
import {TranslateService} from 'ng2-translate';
import { FillDataStepsChangerService, FILL_DATA_STEPS } from '../../services/fill-data-steps-changer.service';
import {HeaderStateService,HEADER_STATE} from '../../services/header-state.service';
import { RouterStateService } from '../../services/router-state.service';
import { routerTransition } from '../../routing/router.animations';

@Component({
  selector: 'app-patrimony',
  templateUrl: './patrimony.component.html',
  styleUrls: ['./patrimony.component.css'],
  animations: [routerTransition()],
  host: {'[@routerTransition]': ''}
})
export class PatrimonyComponent implements OnInit {

  /**
   * @var {array} familySituations
   * @var {string} selectedValue
   * @var {boolean} patrimonyChecked
   * @var {string} checkedItem1
   * @var {string} checkedItem2
   * @var {string} checkedItem3
   * @var {string} checkedItem4
   */
familySituations=[{label:'Madame',value:'md'},{label:'Monsieur',value:'mo'}]
selectedValue : string = "";
patrimonyChecked : boolean = false;
checkedItem1 : string;
checkedItem2 : string;
checkedItem3 : string;
checkedItem4 : string;
@HostBinding('@routerTransition') routerAnimation = "next";

  /**
   * @constructor
   * Constructor of PatrimonyComponent class
   * @param {TranslateService} translate
   * @param {Location} _location location service
   * @param {Router} router router service
   * @param {HeaderStateService} _headerSteps
   * @param {FillDataStepsChangerService} _fillDataStepService 
   */
  constructor(translate: TranslateService,private router: Router,private _location:Location, 
              private _headerSteps:HeaderStateService,
              private _fillDataStepService: FillDataStepsChangerService,
              private _routerState: RouterStateService) { 
    this.routerAnimation = this._routerState.lastAction;
    setTimeout(() => {
      this.routerAnimation = 'next'
    }, 500);
  }

  /**
   * Initialisations
   * @function ngOnInit
   */
  ngOnInit() {
    this._fillDataStepService.setCurrentStep(FILL_DATA_STEPS.SITUATION);
    this._headerSteps.setCurrentState(HEADER_STATE.FILL_DATA);
  }

  /**
   * Move to the previous step
   * @function goBack
   */
  goBack(){
    this._location.back();

  /**
   * Move to the next step
   * @function next
   */
  }
  next(){
    this.routerAnimation = 'prev';
    setTimeout(() => {
      this.router.navigate(['follow-account']);
    },50)
  }

  /**
   * Check one item and remove the check from the other checked item
   * @function changeCheckedItem  
   */
  changeCheckedItem(checkedItem) {
    if(checkedItem == 1) {
      this.checkedItem2 = this.checkedItem3 = this.checkedItem4 = undefined;
    }
    if(checkedItem == 2) {
      this.checkedItem1 = this.checkedItem3 = this.checkedItem4 = undefined;
    }
    if(checkedItem == 3) {
      this.checkedItem2 = this.checkedItem1 = this.checkedItem4 = undefined;
    }
    if(checkedItem == 4) {
      this.checkedItem2 = this.checkedItem3 = this.checkedItem1 = undefined;
    }
  }

}



