import { Component, HostBinding, OnInit, DoCheck } from '@angular/core';
import { Location } from '@angular/common';
import { Router } from '@angular/router';
import { FillDataStepsChangerService, FILL_DATA_STEPS } from '../../services/fill-data-steps-changer.service';
import {HeaderStateService,HEADER_STATE} from '../../services/header-state.service';
import { ScrollService } from '../../services/scroll.service';
import { RouterStateService } from '../../services/router-state.service';
import { routerTransition, holderTransition } from '../../routing/router.animations';
@Component({
  selector: 'app-your-account',
  templateUrl: './your-account.component.html',
  styleUrls: ['./your-account.component.css'],
  animations: [routerTransition(), holderTransition()],
  host: {'[@routerTransition]': ''},
})
export class YourAccountComponent implements OnInit, DoCheck  {

  /**
   * @var {array} fiscalPays
   * @var {string} selectedPays
   * @var {string} issueOrReceiveRegularInternationalTransfersWithinTheNext12Months
   */
  fiscalPays = [{ label: 'France', value: 'fr' }, { label: 'England', value: 'en' }, { label: 'USA', value: 'usa' }, { label: 'Japan', value: 'jp' }]
  selectedPays;
  issueOrReceiveRegularInternationalTransfersWithinTheNext12Months:string[] = [];
  noInternationalTransactions : string = "";
  @HostBinding('@routerTransition') routerAnimation;

    /**
   * @constructor
   * Constructor of YourAccountComponent class
   * @param {Location} _location location service
   * @param {Router} router router service
   * @param {HeaderStateService} _headerSteps
   * @param {FillDataStepsChangerService} _fillDataStepService 
   */
  constructor(private _location: Location, private router: Router, 
              private _headerSteps:HeaderStateService,
              private _fillDataStepService: FillDataStepsChangerService,
              private _routerState: RouterStateService,
              private _scroll: ScrollService) {
    this.routerAnimation = this._routerState.lastAction;
    setTimeout(() => {
      this.routerAnimation = 'next'
    }, 500);
  }

/**
 * Initialisations
 * @function ngOnInit
 */
  ngOnInit() {
    this._fillDataStepService.setCurrentStep(FILL_DATA_STEPS.ACCOUNT);
    this._headerSteps.setCurrentState(HEADER_STATE.FILL_DATA);
  }

  ngDoCheck() {
    if(this.noInternationalTransactions == "No") {
      console.log("change")
      this.issueOrReceiveRegularInternationalTransfersWithinTheNext12Months = []
    } 
    this.noInternationalTransactions = "";
    
  }

  exist(str) {
    return (this.issueOrReceiveRegularInternationalTransfersWithinTheNext12Months.indexOf(str) > -1)
  }


  /**
   * Move to the previous step
   * @function goBack
   */
  goBack() {
    this._location.back();
  }

  /**
   * Move to the next step
   * @function next
   */
  next() {
    this.routerAnimation = 'prev';
    setTimeout(() => {
      this.router.navigate(['card-type']);
    }, 50);
  }
}



