import { Component, OnInit, DoCheck, OnDestroy, Input, forwardRef, NgModule, Output, EventEmitter, ViewChild, ElementRef, SimpleChanges } from '@angular/core';
import { CommonModule } from '@angular/common';
import { PibTextFieldModule } from './text-field.component';
import { PibDropdownModule } from './dropdown.component';
import { ControlValueAccessor, NgModel, NG_VALUE_ACCESSOR, FormsModule } from "@angular/forms";
import { SelectItem } from '../common/api';
import { TranslateService } from 'ng2-translate';

//function
const noop = (_?: any) => { };
@Component({
  selector: 'pib-drop-down-editable',
  template: `<div  [ngClass]="{'dropdown-container clearfix':true,'form__input--error':messageType == 'ERROR' && hasErrors() && (inputTf.inputTf.dirty || stepSubmitted),
                                                                  'form__input--success': !hasErrors() && !inputTf.inputTf.pristine,
                                                                  'form__input--warning':messageType == 'WARNING' && hasErrors() && (inputTf.inputTf.dirty || stepSubmitted)}">
                   <pib-dropdown [options]="options"
                               [style]="dropDownStyle"
                               placeholder="FR"
                               [styleClass]="dropDownClass + ' drop__down'"
                               (onChange)="dropDownChange($event)"
                               [(ngModel)]="_multiSelectValue"></pib-dropdown>
                 <span class="dropdown-info">{{multiSelectValue}}</span>
                 <pib-text-field #inputTf (change)="change.emit($event)" (blur)="blur.emit($event)" 
                                 [placeholder]="placeholder"
                                 [label]="label"
                                 [name]="name"
                                 [type]="type"
                                 [(ngModel)]="value" 
                                 [disabled]="disabled"
                                 [inputClass]="inputClass + ' inputTF'"
                                 inputStyle="inputStyle"
                                 (focus)="focus.emit($event)" 
                                 (hover)="hover.emit($event)"></pib-text-field>
              </div>
               <div class="form__message" [ngClass] = "{'form__message--error':messageType == 'ERROR',
                                              'form__message--warning':messageType == 'WARNING'}"
                                  *ngIf="hasErrors() && (inputTf.inputTf.dirty || stepSubmitted)">
                                    <i class="icon" [ngClass] = "{'icon-circle-delete' :messageType == 'ERROR',
                                        'icon-circle-error': messageType == 'WARNING'}"></i>
                                  {{messageTrad}}
                </div>`,
  providers: [
    { provide: NG_VALUE_ACCESSOR, useExisting: forwardRef(() => DropDownEditable), multi: true }
  ],
  styles: [
    `.drop__down{
       border:none important!;
    }
      .inputTF{
        float:right;
        border:none important!;
      }
    `
  ]
})

export class DropDownEditable implements ControlValueAccessor {
  protected onModelChange: (_: any) => void = noop;
  protected onModelTouched: () => void = noop;
  protected _value: any;
  protected _multiSelectValue: string;
  @Input() options: SelectItem[] = [];
  @Input() public placeholder: string;
  @Input() public label: string;
  @Input() public name: string;
  @Input() public message: any;
  @Input() public type: string = 'text';
  @Input() public messageType: string = 'ERROR';
  @Input() public disabled: boolean = false;
  @Input() public inputStyle: string = '';
  @Input() public inputClass: string = '';
  @Input() get multiSelectValue(){
    return this._multiSelectValue
  }
  @Input() public stepSubmitted: boolean;
  @Output() public  multiSelectValueChange: EventEmitter<string>;
  @Output() public change: EventEmitter<any> = new EventEmitter();//event fired when the model (text in input) change
  @Output() public blur: EventEmitter<any> = new EventEmitter();//event fired when the input get blurred
  @Output() public focus: EventEmitter<any> = new EventEmitter();//event fired when the input get focused
  @Output() public hover: EventEmitter<any> = new EventEmitter();//event fired when the input get hovered
  //DropDown
  @Input() dropDownStyle: any;
  @Input() dropDownClass: string;
  @ViewChild('inputTf') inputTf: ElementRef;
  private messageTrad: string;

  constructor(private _translate: TranslateService) { 
    this.multiSelectValueChange = new EventEmitter();
    this._translate.onLangChange.subscribe(() => {
      if(this.message){
        this.updateTrad(this.message);
      }
    })
  }

  ngOnChanges(changes: SimpleChanges){
    if(changes['message'] && changes['message'].currentValue){
      this.updateTrad(changes['message'].currentValue);
    }
  }

  updateTrad(key){
    this._translate.get(key).subscribe((value) => {
      this.messageTrad = value;
    })  
  }
  
  set multiSelectValue(val) {
    if(val && val != this._multiSelectValue){
      this._multiSelectValue = val;
      this.multiSelectValueChange.emit(this._multiSelectValue);
    }
  }
  dropDownChange($event) {
    this.multiSelectValueChange.emit(this._multiSelectValue);
  }
  /**
   * get the input value
   * @returns {any}
   */
  get value(): any {
    return this._value;
  }

  /**
   * set the input text
   * @param value
   */
  set value(value: any) {
    if (value !== this._value) {
      this._value = value;
      this.change.emit({ value: this._value});
      this.onModelChange(this._value);
      this.multiSelectValueChange.emit(this._multiSelectValue);
    }
  }

  /**
   * write value (from interface ControlValueAccessor)
   * @param value
   */
  public writeValue(value: any) {
    if (value !== this._value) {
      this._value = value;
    }
  }

  /**
   * register onChange event
   * @param fn
   */
  public registerOnChange(fn: (_: any) => void) {
    this.onModelChange = fn;
  }
  
  /**
   * register onTouch event
   * @param fn
   */
  public registerOnTouched(fn: () => void) {
    this.onModelTouched = fn;
  }

  /**
   * return true if the input has errors
   * @returns {boolean}
   */
  public hasErrors(): boolean {
    return this.message != null && this.message.trim().length > 0;
  }

  /**
   * blur the input
   */
  public blurTextField() {
    this.inputTf.nativeElement.blurTextField()
  }

  /**
   * focus the input
   */
  public focusTextField() {
    this.inputTf.nativeElement.focusTextField()
  }
}

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    PibTextFieldModule,
    PibDropdownModule
  ],
  exports: [DropDownEditable],
  declarations: [DropDownEditable]
})
export class PibDropDownEditableModule { }



