import {NgModule,Component,OnDestroy,Input,EventEmitter} from '@angular/core';
import {CommonModule} from '@angular/common';
import {DomHandler} from '../common/domhandler';
import {MenuItem} from '../common/api';

@Component({
    selector: 'p-tabMenu',
    template: `
        <div [ngClass]="'tabmenu'" [ngStyle]="style" [class]="styleClass">
            <ul class="tabmenu__nav" role="tablist">
                <li *ngFor="let item of model" 
                    [ngClass]="{'tabmenu__item':true,'tabmenu__item--disabled':item.disabled,
                        'tabmenu__item--hasicon':item.icon,'active':activeItem==item}">
                    <a [href]="item.url||'#'" class="ui-menuitem-link ui-corner-all" (click)="itemClick($event,item)">
                        <span class="ui-menuitem-icon fa" [ngClass]="item.icon"></span>
                        <span class="ui-menuitem-text">{{item.label}}</span>
                    </a>
                </li>
            </ul>
        </div>
    `,
    providers: [DomHandler]
})
export class TabMenu implements OnDestroy {

    @Input() model: MenuItem[];

    @Input() activeItem: MenuItem;

    @Input() style: any;

    @Input() styleClass: string;

    constructor() {}

    ngOnInit() {
        if(!this.activeItem && this.model && this.model.length) {
            this.activeItem = this.model[0];
        }
    }

    itemClick(event: Event, item: MenuItem) {
        if(item.disabled) {
            event.preventDefault();
            return;
        }

        if(!item.url||item.routerLink) {
            event.preventDefault();
        }

        if(item.command) {
            if(!item.eventEmitter) {
                item.eventEmitter = new EventEmitter();
                item.eventEmitter.subscribe(item.command);
            }

            item.eventEmitter.emit({
                originalEvent: event,
                item: item
            });
        }

        this.activeItem = item;
    }

    ngOnDestroy() {
        if(this.model) {
            for(let item of this.model) {
                this.unsubscribe(item);
            }
        }
    }

    unsubscribe(item: any) {
        if(item.eventEmitter) {
            item.eventEmitter.unsubscribe();
        }

        if(item.items) {
            for(let childItem of item.items) {
                this.unsubscribe(childItem);
            }
        }
    }

}

@NgModule({
    imports: [CommonModule],
    exports: [TabMenu],
    declarations: [TabMenu]
})
export class PibTabMenuModule { }



