import { Injectable } from '@angular/core';

@Injectable()
export class ScrollService {
  private isScrolling = false;


  constructor() { }

  // Element to move, time in ms to animate
  scrollTo(element, duration) {
    if(!this.isScrolling){
      this.isScrolling = true;
      var e = document.documentElement;
      if(e.scrollTop===0){
          var t = e.scrollTop;
          ++e.scrollTop;
          e = t+1===e.scrollTop--?e:document.body;
      }
      this.scrollToC(e, e.scrollTop, element, duration);
    }
      
  }

  // Element to move, element or px from, element or px to, time in ms to animate
  scrollToC(element, from, to, duration) {
      if (duration <= 0) return;
      if(typeof from === "object")from=from.offsetTop;
      if(typeof to === "object")to=to.offsetTop;

      this.scrollToX(element, from, to, 0, 1/duration, 20, this.easeOutCuaic);
  }

   scrollToX(element, xFrom, xTo, t01, speed, step, motion) {
      if (t01 < 0 || t01 > 1 || speed<= 0) {
          element.scrollTop = xTo;
          this.isScrolling = false;
          return;
      }
      element.scrollTop = xFrom - (xFrom - xTo) * motion(t01);
      t01 += speed * step;

      setTimeout(() => {
          this.scrollToX(element, xFrom, xTo, t01, speed, step, motion);
      }, step);
  }
   easeOutCuaic(t){
      t--;
      return t*t*t+1;
  }

 



  scrollToFirstBadField(){
    var badFields = window.document.getElementsByClassName('form__input--error');
    var top =window.pageYOffset;
    if(badFields[0]){
      this.scrollTo( badFields[0].getBoundingClientRect().top + document.body.scrollTop - 50, 1000);
    }
  }

}



