import { NgModule, Component, ElementRef, AfterViewInit, OnDestroy, OnInit, Input, Output, EventEmitter, forwardRef, Renderer, trigger, state, style, transition, animate, ViewChild } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DomHandler } from '../common';
import { AbstractControl, NG_VALUE_ACCESSOR, NG_VALIDATORS, ControlValueAccessor } from '@angular/forms';
import { PibTextInputModule, PibButtonModule } from '../directives';


export const CALENDAR_VALUE_ACCESSOR: any = {
  provide: NG_VALUE_ACCESSOR,
  useExisting: forwardRef(() => CalendarComponent),
  multi: true
};

export const CALENDAR_VALIDATOR: any = {
  provide: NG_VALIDATORS,
  useExisting: forwardRef(() => CalendarComponent),
  multi: true
};
/**
 * setting local
 */
export interface LocaleSettings {
  firstDayOfWeek?: number;
  dayNames: string[];
  dayNamesShort: string[];
  dayNamesMin: string[];
  monthNames: string[];
  monthNamesShort: string[];
}

@Component({
  selector: 'pib-calendar',
  template: `
              <span  [ngClass]="{'ui-calendar':true,'ui-calendar-w-btn':showIcon,'ui-overlay-visible':overlayVisible,'ui-calendar--range':selectRange}" [ngStyle]="style" [class]="styleClass">
                  <template [ngIf]="!inline">
                      <button  type="button" [icon]="icon" pibButton *ngIf="showIcon && leftIcon" (click)="onButtonClick($event,inputfield)"
                               [ngClass]="{'ui-datepicker-trigger':true,'ui-state-disabled':disabled,'no-border':hideTextInput && !overlayVisible,'left-icon':overlayVisible && leftIcon}" [disabled]="disabled"></button>
                      <input #inputfield (keyup)="keyup.emit($event)" type="text" [attr.required]="required" pibTextInput [value]="inputFieldValue" (focus)="onInputFocus(inputfield, $event)" (keydown)="onInputKeydown($event)" (click)="closeOverlay=false" (blur)="onInputBlur($event)"
                             [readonly]="readonlyInput || hideTextInput"  [ngClass]="{'border-right-input':!showIcon && !hideTextInput,'hidden-text-input':hideTextInput && !overlayVisible,'right-input':overlayVisible && leftIcon}"
                             (input)="onInput($event)" [ngStyle]="inputStyle" [class]="inputStyleClass" [placeholder]="placeholder||''" [disabled]="disabled" [attr.tabindex]="tabindex"
                             ><button type="button" [icon]="icon" pibButton *ngIf="showIcon && !leftIcon" (click)="onButtonClick($event,inputfield)"
                               [ngClass]="{'ui-datepicker-trigger':true,'ui-state-disabled':disabled,'no-border':hideTextInput && !overlayVisible}" [disabled]="disabled"></button>
                  </template>
                  <div #datepicker class="ui-datepicker ui-widget ui-widget-content ui-helper-clearfix ui-corner-all" [ngClass]="{'ui-datepicker-inline':inline,'ui-shadow':!inline,'ui-state-disabled':disabled,'ui-datepicker-timeonly':timeOnly}"
                       [ngStyle]="{'display': inline ? 'inline-block' : (overlayVisible ? 'block' : 'none')}" (click)="onDatePickerClick($event)" [@overlayState]="inline ? 'visible' : (overlayVisible ? 'visible' : 'hidden')">
                      <div class="ui-datepicker-header ui-widget-header ui-helper-clearfix ui-corner-all" *ngIf="!timeOnly && (overlayVisible || inline)">
                          <ng-content select="p-header"></ng-content>
                          <a class="ui-datepicker-prev-year ui-corner-all" (click)="previousYear()">
                              <i class="icon icon-chevron-double-left"></i>
                          </a>
                          <a class="ui-datepicker-prev ui-corner-all" (click)="prevMonth($event)">
                             <i class="icon icon-chevron-left"></i>
                          </a>
                          <a class="ui-datepicker-next ui-corner-all"  (click)="nextMonth($event)">
                              <i class="icon icon-chevron-right"></i>
                          </a>
                          <a class="ui-datepicker-next-year ui-corner-all"  (click)="nextYear()">
                              <i class="icon icon-chevron-double-right"></i>
                          </a>
                          <div class="ui-datepicker-title">
                              <span class="ui-datepicker-month" *ngIf="!monthNavigator">{{locale.monthNames[currentMonth]}}</span>
                              <select class="ui-datepicker-month" *ngIf="monthNavigator" (change)="onMonthDropdownChange($event.target.value)">
                                  <option [value]="i" *ngFor="let month of locale.monthNames;let i = index" [selected]="i == currentMonth">{{month}}</option>
                              </select>
                              <select class="ui-datepicker-year" *ngIf="yearNavigator" (change)="onYearDropdownChange($event.target.value)">
                                  <option [value]="year" *ngFor="let year of yearOptions" [selected]="year == currentYear">{{year}}</option>
                              </select>
                              <span class="ui-datepicker-year" *ngIf="!yearNavigator">{{currentYear}}</span>
                          </div>
                      </div>
                      <table class="ui-datepicker-calendar" *ngIf="!timeOnly && (overlayVisible || inline)">
                          <thead>
                              <tr>
                                  <th scope="col" *ngFor="let weekDay of locale.dayNamesMin;let begin = first; let end = last">
                                      <span>{{weekDay}}</span>
                                  </th>
                              </tr>
                          </thead>
                          <tbody>
                              <tr *ngFor="let week of dates">
                                  <td *ngFor="let date of week" [ngClass]="{'ui-datepicker-other-month ui-state-disabled':date.otherMonth,
                                      'ui-datepicker-current-day':isSelected(date),'ui-datepicker-today':date.today && !value,'ui-state-in-range':isInRange(date)}">
                                      <a class="ui-state-default"  *ngIf="date.otherMonth ? showOtherMonths : true"
                                         [ngClass]="{'ui-state-active':isSelected(date), 'ui-state-highlight':date.today,'ui-state-disabled':!date.selectable}"
                                         (click)="onDateSelect($event,date)">{{date.day}}</a>
                                  </td>
                              </tr>
                          </tbody>
                      </table>
                      <div class="ui-timepicker ui-widget-header ui-corner-all" *ngIf="showTime||timeOnly">
                          <div class="ui-hour-picker">
                              <a  (click)="incrementHour($event)">
                                  <span class="fa fa-angle-up"></span>
                              </a>
                              <span [ngStyle]="{'display': currentHour < 10 ? 'inline': 'none'}">0</span><span>{{currentHour}}</span>
                              <a  (click)="decrementHour($event)">
                                  <span class="fa fa-angle-down"></span>
                              </a>
                          </div>
                          <div class="ui-separator">
                              <a >
                                  <span class="fa fa-angle-up"></span>
                              </a>
                              <span>:</span>
                              <a >
                                  <span class="fa fa-angle-down"></span>
                              </a>
                          </div>
                          <div class="ui-minute-picker">
                              <a  (click)="incrementMinute($event)">
                                  <span class="fa fa-angle-up"></span>
                              </a>
                              <span [ngStyle]="{'display': currentMinute < 10 ? 'inline': 'none'}">0</span><span>{{currentMinute}}</span>
                              <a  (click)="decrementMinute($event)">
                                  <span class="fa fa-angle-down"></span>
                              </a>
                          </div>
                          <div class="ui-separator" *ngIf="showSeconds">
                              <a >
                                  <span class="fa fa-angle-up"></span>
                              </a>
                              <span>:</span>
                              <a >
                                  <span class="fa fa-angle-down"></span>
                              </a>
                          </div>
                          <div class="ui-second-picker" *ngIf="showSeconds">
                              <a  (click)="incrementSecond($event)">
                                  <span class="fa fa-angle-up"></span>
                              </a>
                              <span [ngStyle]="{'display': currentSecond < 10 ? 'inline': 'none'}">0</span><span>{{currentSecond}}</span>
                              <a  (click)="incrementSecond($event)">
                                  <span class="fa fa-angle-down"></span>
                              </a>
                          </div>
                          <div class="ui-ampm-picker" *ngIf="hourFormat=='12'">
                              <a  (click)="toggleAMPM($event)">
                                  <span class="fa fa-angle-up"></span>
                              </a>
                              <span>{{pm ? 'PM' : 'AM'}}</span>
                              <a  (click)="toggleAMPM($event)">
                                  <span class="fa fa-angle-down"></span>
                              </a>
                          </div>
                      </div>
                      <ng-content select="p-footer"></ng-content>
                  </div>
              </span>
  `,
  styles: [
    `
      .border-right-input{
        border-right: 1px solid #404040;
      }
      .left-icon{
        border-left:  1px solid #404040;
        border-right: none;
      }
      .right-input{
        left: 40px;
        border-left: none;
        border-right:  1px solid #404040;
      }
      .hidden-text-input{
        opacity: 0;
        width:0px;
        margin-left:0px;
        margin-right:0px;
        padding:0px;
      }
      .no-border{
        border:none;
        width: auto;
        background:transparent;
      }
    `
  ],
  animations: [
    trigger('overlayState', [
      state('hidden', style({
        opacity: 0
      })),
      state('visible', style({
        opacity: 1
      })),
      transition('visible => hidden', animate('400ms ease-in')),
      transition('hidden => visible', animate('400ms ease-out'))
    ])
  ],
  host: {
    '[class.ui-inputwrapper-filled]': 'filled',
    '[class.ui-inputwrapper-focus]': 'focus'
  },
  providers: [DomHandler, CALENDAR_VALUE_ACCESSOR, CALENDAR_VALIDATOR]
})
export class CalendarComponent implements AfterViewInit, OnInit, OnDestroy, ControlValueAccessor {

  @Input() defaultDate: Date;//set a default date

  @Input() style: string = '';//set a calendar style

  @Input() selectRange: boolean;//selecting a range

  @Input() styleClass: string = '';//set a calendar style class

  @Input() hideTextInput: boolean = false;

  @Input() inputStyle: string = '';//set the input style

  @Input() inputStyleClass: string = '';//set the input style class

  @Input() placeholder: string;//input placeholder

  @Input() disabled: any;//input disabled

  @Input() dateFormat: string = 'mm/dd/yy';//date format

  @Input() inline: boolean = false;//show the calendar inline

  @Input() showOtherMonths: boolean = true;//show other months's days

  @Input() selectOtherMonths: boolean = true;//can user select other months's days

  @Input() leftIcon: boolean = false; // show icon at the left of the input

  @Input() showIcon: boolean;//show icon in the input

  @Input() icon: string = 'fa-calendar';//icon class

  @Input() appendTo: any;

  @Input() readonlyInput: boolean;//set the input to readonly

  @Input() shortYearCutoff: any = '+10';

  @Input() monthNavigator: boolean;//show months selection

  @Input() yearNavigator: boolean;//show year selection

  @Input() yearRange: string;//range of years

  @Input() showTime: boolean;//show time selection

  @Input() hourFormat: string = '24';

  @Input() timeOnly: boolean;//show time only

  @Input() stepHour: number = 1;//increment hour by stepHour

  @Input() stepMinute: number = 1;//increment hour by stepMinute

  @Input() stepSecond: number = 1;//increment hour by stepSecond

  @Input() showSeconds: boolean = false;//show seconds in time selector

  @Input() required: boolean;//is calendar required

  @Input() showOnFocus: boolean = true;//show calendar when input gain focus

  @Input() dataType: string = 'date';//data type date or string (value of : 'date', 'string')

  @Output() onFocus: EventEmitter<any> = new EventEmitter();//event fired when input gain focus

  @Output() onBlur: EventEmitter<any> = new EventEmitter();//event fired when input get blurred

  @Output() onSelect: EventEmitter<any> = new EventEmitter();//event fired when date is selected

  @Output() public keyup: EventEmitter<any> = new EventEmitter();//event fired when the model (text in input) change

  //local setting
  protected _locale;
  @Input() get locale() {
    if(!this._locale){
      this._locale = {
          firstDayOfWeek: 0,
          dayNames: ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"],
          dayNamesShort: ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"],
          dayNamesMin: ["Su", "Mo", "Tu", "We", "Th", "Fr", "Sa"],
          monthNames: ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"],
          monthNamesShort: ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"]
      }
    }
    return this._locale
  };
  set locale(val) {
    if (val) {
      this._locale = val
      this.createMonth(this.currentMonth, this.currentYear);
    } else {
        this._locale = {
          firstDayOfWeek: 0,
          dayNames: ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"],
          dayNamesShort: ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"],
          dayNamesMin: ["Su", "Mo", "Tu", "We", "Th", "Fr", "Sa"],
          monthNames: ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"],
          monthNamesShort: ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"]
      }
      this.createMonth(this.currentMonth, this.currentYear);
    }
  }



  @Input() tabindex: number;

  @ViewChild('datepicker') overlayViewChild: ElementRef;

  value: Date;

  dateEnd: Date;

  dates: any[];

  weekDays: string[] = [];

  currentMonthText: string;

  currentMonth: number;

  currentYear: number;

  currentHour: number;

  currentMinute: number;

  currentSecond: number;

  pm: boolean;

  overlay: HTMLDivElement;

  overlayVisible: boolean;

  closeOverlay: boolean = true;

  dateClick: boolean;

  onModelChange: Function = () => { };

  onModelTouched: Function = () => { };

  calendarElement: any;

  documentClickListener: any;

  ticksTo1970: number;

  yearOptions: number[];

  focus: boolean;

  filled: boolean;

  inputFieldValue: string = null;

  _minDate: Date;

  _maxDate: Date;

  _isValid: boolean = true;

  /**
   * get min date
   * @returns {Date}
   */
  @Input() get minDate(): Date {
    return this._minDate;
  }

  /**
   * set min date
   * @param date
   */
  set minDate(date: Date) {
    this._minDate = date;
    this.createMonth(this.currentMonth, this.currentYear);
  }

  /**
   * get max date
   * @returns {Date}
   */
  @Input() get maxDate(): Date {
    return this._maxDate;
  }

  /**
   * set max date
   * @param date
   */
  set maxDate(date: Date) {
    this._maxDate = date;
    this.createMonth(this.currentMonth, this.currentYear);
  }

  constructor(public el: ElementRef, public domHandler: DomHandler, public renderer: Renderer) { }

  ngOnInit() {
    let date = this.defaultDate || new Date();
    let dayIndex = this.locale.firstDayOfWeek;
    //setup week's days name from local setting
    for (let i = 0; i < 7; i++) {
      this.weekDays.push(this.locale.dayNamesMin[dayIndex]);
      dayIndex = (dayIndex == 6) ? 0 : ++dayIndex;
    }
    //get current month
    this.currentMonth = date.getMonth();
    //get current year
    this.currentYear = date.getFullYear();
    if (this.showTime) {
      this.currentMinute = date.getMinutes();
      this.currentSecond = date.getSeconds();
      this.pm = date.getHours() > 11;

      if (this.hourFormat == '12')
        this.currentHour = date.getHours() == 0 ? 12 : date.getHours() % 12;
      else
        this.currentHour = date.getHours();
    }
    else if (this.timeOnly) {
      this.currentMinute = 0;
      this.currentHour = 0;
      this.currentSecond = 0;
    }
    //create month
    this.createMonth(this.currentMonth, this.currentYear);

    this.ticksTo1970 = (((1970 - 1) * 365 + Math.floor(1970 / 4) - Math.floor(1970 / 100) +
      Math.floor(1970 / 400)) * 24 * 60 * 60 * 10000000);

    if (this.yearNavigator && this.yearRange) {
      this.yearOptions = [];
      let years = this.yearRange.split(':'),
        yearStart = parseInt(years[0]),
        yearEnd = parseInt(years[1]);

      for (let i = yearStart; i <= yearEnd; i++) {
        this.yearOptions.push(i);
      }
    }
  }

  ngAfterViewInit() {
    this.overlay = <HTMLDivElement>this.overlayViewChild.nativeElement;

    if (!this.inline && this.appendTo) {
      if (this.appendTo === 'body')
        document.body.appendChild(this.overlay);
      else
        this.domHandler.appendChild(this.overlay, this.appendTo);
    }
  }

  createMonth(month: number, year: number) {
    this.dates = [];
    this.currentMonth = month;
    this.currentYear = year;
    this.currentMonthText = this.locale.monthNames[month];
    let firstDay = this.getFirstDayOfMonthIndex(month, year);
    let daysLength = this.getDaysCountInMonth(month, year);
    let prevMonthDaysLength = this.getDaysCountInPrevMonth(month, year);
    let sundayIndex = this.getSundayIndex();
    let dayNo = 1;
    let today = new Date();

    for (let i = 0; i < 6; i++) {
      let week = [];

      if (i == 0) {
        for (let j = (prevMonthDaysLength - firstDay + 1); j <= prevMonthDaysLength; j++) {
          let prev = this.getPreviousMonthAndYear(month, year);
          week.push({
            day: j, month: prev.month, year: prev.year, otherMonth: true,
            today: this.isToday(today, j, prev.month, prev.year), selectable: this.isSelectable(j, prev.month, prev.year)
          });
        }

        let remainingDaysLength = 7 - week.length;
        for (let j = 0; j < remainingDaysLength; j++) {
          week.push({
            day: dayNo, month: month, year: year, today: this.isToday(today, dayNo, month, year),
            selectable: this.isSelectable(dayNo, month, year)
          });
          dayNo++;
        }
      }
      else {
        for (let j = 0; j < 7; j++) {
          if (dayNo > daysLength) {
            let next = this.getNextMonthAndYear(month, year);
            week.push({
              day: dayNo - daysLength, month: next.month, year: next.year, otherMonth: true,
              today: this.isToday(today, dayNo - daysLength, next.month, next.year),
              selectable: this.isSelectable((dayNo - daysLength), next.month, next.year)
            });
          }
          else {
            week.push({
              day: dayNo, month: month, year: year, today: this.isToday(today, dayNo, month, year),
              selectable: this.isSelectable(dayNo, month, year)
            });
          }

          dayNo++;
        }
      }

      this.dates.push(week);
    }
  }

  prevMonth(event) {
    if (this.disabled) {
      event.preventDefault();
      return;
    }

    if (this.currentMonth === 0) {
      this.currentMonth = 11;
      this.currentYear--;
    }
    else {
      this.currentMonth--;
    }

    this.createMonth(this.currentMonth, this.currentYear);
    event.preventDefault();
  }

  nextMonth(event) {
    if (this.disabled) {
      event.preventDefault();
      return;
    }

    if (this.currentMonth === 11) {
      this.currentMonth = 0;
      this.currentYear++;
    }
    else {
      this.currentMonth++;
    }

    this.createMonth(this.currentMonth, this.currentYear);
    event.preventDefault();
  }

  onDateSelect(event, dateMeta) {
    if (this.disabled || !dateMeta.selectable) {
      event.preventDefault();
      return;
    }
    if (this.selectRange) {
      if (this.value) {
        //TODO check if start date (value) is less than selected date
        this.selectValidRangeDate(event, dateMeta);

      } else {
        //TODO select start date here
        this.selectStartRangeDate(event, dateMeta);
      }
    } else {
      this.selectSelectableDate(event, dateMeta);
    }

  }
  selectValidRangeDate(event, dateMeta) {
    let endvalue = new Date(dateMeta.year, dateMeta.month, dateMeta.day);
    if (this.showTime) {
      if (this.hourFormat === '12' && this.pm && this.currentHour != 12)
        endvalue.setHours(this.currentHour + 12);
      else
        endvalue.setHours(this.currentHour);

      endvalue.setMinutes(this.currentMinute);
      endvalue.setSeconds(this.currentSecond);
    }
    if (this.value.getTime() >= endvalue.getTime()) {
      this.selectStartRangeDate(event, dateMeta)
    } else {
      this.selectEndRangeDate(event, dateMeta)
    }
  }
  selectStartRangeDate(event, dateMeta) {
    this.dateEnd = null
    if (dateMeta.otherMonth) {
      if (this.selectOtherMonths)
        this.selectDate(dateMeta);
    }
    else {
      this.selectDate(dateMeta);
    }
    this.updateInputfield();
    event.stopPropagation()
  }
  selectEndRangeDate(event, dateMeta) {
    if (dateMeta.otherMonth) {
      if (this.selectOtherMonths)
        this.selectEndDate(dateMeta);
    }
    else {
      this.selectEndDate(dateMeta);
    }
    this.updateInputfield();
    event.stopPropagation()
  }
  selectSelectableDate(event, dateMeta) {
    if (dateMeta.otherMonth) {
      if (this.selectOtherMonths)
        this.selectDate(dateMeta);
    }
    else {
      this.selectDate(dateMeta);
    }

    this.dateClick = true;
    this.updateInputfield();
    event.preventDefault();
  }

  updateInputfield() {
    if (this.selectRange) {
      if (this.value) {
        let formattedValue;

        if (this.timeOnly) {
          formattedValue = this.formatTime(this.value);
        }
        else {
          formattedValue = this.formatDate(this.value, this.dateFormat);
          if (this.showTime) {
            formattedValue += ' ' + this.formatTime(this.value);
          }
        }
        if (this.dateEnd) {
          let endFormat;

          if (this.timeOnly) {
            endFormat = this.formatTime(this.dateEnd);
          }
          else {
            endFormat = this.formatDate(this.dateEnd, this.dateFormat);
            if (this.showTime) {
              endFormat += ' ' + this.formatTime(this.dateEnd);
            }
          }
          formattedValue += '-' + endFormat
        }
        this.inputFieldValue = formattedValue;
      }
      else {
        this.inputFieldValue = '';
      }
    } else {
      if (this.value) {
        let formattedValue;

        if (this.timeOnly) {
          formattedValue = this.formatTime(this.value);
        }
        else {
          formattedValue = this.formatDate(this.value, this.dateFormat);
          if (this.showTime) {
            formattedValue += ' ' + this.formatTime(this.value);
          }
        }

        this.inputFieldValue = formattedValue;
      }
      else {
        this.inputFieldValue = '';
      }
    }


    this.updateFilledState();
  }

  //select date and update input (start date if range)
  selectDate(dateMeta) {
    this.value = new Date(dateMeta.year, dateMeta.month, dateMeta.day);
    if (this.showTime) {
      if (this.hourFormat === '12' && this.pm && this.currentHour != 12)
        this.value.setHours(this.currentHour + 12);
      else
        this.value.setHours(this.currentHour);

      this.value.setMinutes(this.currentMinute);
      this.value.setSeconds(this.currentSecond);
    }
    this._isValid = true;
    this.updateModel();
    this.onSelect.emit(this.value);
    this.keyup.emit(this.value)
  }
  //select end date if selecting range
  selectEndDate(dateMeta) {
    this.dateEnd = new Date(dateMeta.year, dateMeta.month, dateMeta.day);
    if (this.showTime) {
      if (this.hourFormat === '12' && this.pm && this.currentHour != 12)
        this.dateEnd.setHours(this.currentHour + 12);
      else
        this.dateEnd.setHours(this.currentHour);

      this.dateEnd.setMinutes(this.currentMinute);
      this.dateEnd.setSeconds(this.currentSecond);
    }
    this._isValid = true;
    this.updateModel();
    this.onSelect.emit({ start: this.value, end: this.dateEnd });
  }
  //update calendar model
  updateModel() {
    if (this.dataType == 'date') {
      if (this.selectRange) {
        this.onModelChange([this.value, this.dateEnd]);
      } else {
        this.onModelChange(this.value);
      }

    }
    else if (this.dataType == 'string') {
      if (this.selectRange) {
        this.onModelChange(this.formatDate(this.value, this.dateFormat) + "-" + this.formatDate(this.dateEnd, this.dateFormat));
      } else {
        this.onModelChange(this.formatDate(this.value, this.dateFormat));
      }

    }

  }

  getFirstDayOfMonthIndex(month: number, year: number) {
    let day = new Date();
    day.setDate(1);
    day.setMonth(month);
    day.setFullYear(year);

    let dayIndex = day.getDay() + this.getSundayIndex();
    return dayIndex >= 7 ? dayIndex - 7 : dayIndex;
  }

  getDaysCountInMonth(month: number, year: number) {
    return 32 - this.daylightSavingAdjust(new Date(year, month, 32)).getDate();
  }

  getDaysCountInPrevMonth(month: number, year: number) {
    let prev = this.getPreviousMonthAndYear(month, year);
    return this.getDaysCountInMonth(prev.month, prev.year);
  }

  getPreviousMonthAndYear(month: number, year: number) {
    let m, y;

    if (month === 0) {
      m = 11;
      y = year - 1;
    }
    else {
      m = month - 1;
      y = year;
    }
    return { 'month': m, 'year': y };
  }

  getNextMonthAndYear(month: number, year: number) {
    let m, y;

    if (month === 11) {
      m = 0;
      y = year + 1;
    }
    else {
      m = month + 1;
      y = year;
    }

    return { 'month': m, 'year': y };
  }

  getSundayIndex() {
    return this.locale.firstDayOfWeek > 0 ? 7 - this.locale.firstDayOfWeek : 0;
  }

  isSelected(dateMeta): boolean {
    if (this.value)
      return this.value.getDate() === dateMeta.day && this.value.getMonth() === dateMeta.month && this.value.getFullYear() === dateMeta.year;
    else
      return false;
  }
  isInRange(dateMeta): boolean {
    if (dateMeta) {
      let dateVal = new Date(dateMeta.year, dateMeta.month, dateMeta.day);
      return dateVal && this.selectRange && this.value && this.dateEnd && ((dateVal.getTime() >= this.value.getTime()) && (dateVal.getTime() <= this.dateEnd.getTime()))
    } else {
      return false
    }

  }

  isToday(today, day, month, year): boolean {
    return today.getDate() === day && today.getMonth() === month && today.getFullYear() === year;
  }

  isSelectable(day, month, year): boolean {
    let validMin = true;
    let validMax = true;

    if (this.minDate) {
      if (this.minDate.getFullYear() > year) {
        validMin = false;
      }
      else if (this.minDate.getFullYear() === year) {
        if (this.minDate.getMonth() > month) {
          validMin = false;
        }
        else if (this.minDate.getMonth() === month) {
          if (this.minDate.getDate() > day) {
            validMin = false;
          }
        }
      }
    }

    if (this.maxDate) {
      if (this.maxDate.getFullYear() < year) {
        validMax = false;
      }
      else if (this.maxDate.getFullYear() === year) {
        if (this.maxDate.getMonth() < month) {
          validMax = false;
        }
        else if (this.maxDate.getMonth() === month) {
          if (this.maxDate.getDate() < day) {
            validMax = false;
          }
        }
      }
    }

    return validMin && validMax;
  }

  onInputFocus(inputfield, event) {
    this.focus = true;
    if (this.showOnFocus) {
      this.showOverlay(inputfield);
    }
    this.onFocus.emit(event);
  }

  onInputBlur(event) {
    this.focus = false;
    this.onBlur.emit(event);
    this.onModelTouched();
  }

  onButtonClick(event, inputfield) {
    this.closeOverlay = false;

    if (!this.overlay.offsetParent) {
      if (!this.hideTextInput) {
        inputfield.focus();
      }
      this.showOverlay(inputfield);
    }
    else
      this.closeOverlay = true;
  }

  onInputKeydown(event) {
    if (event.keyCode === 9) {
      this.overlayVisible = false;
    }
  }

  onMonthDropdownChange(m: string) {
    this.currentMonth = parseInt(m);
    this.createMonth(this.currentMonth, this.currentYear);
  }

  onYearDropdownChange(y: string) {
    this.currentYear = parseInt(y);
    this.createMonth(this.currentMonth, this.currentYear);
  }
  nextYear() {
    this.currentYear += 1;
    this.createMonth(this.currentMonth, this.currentYear);
  }
  previousYear() {
    this.currentYear -= 1;
    this.createMonth(this.currentMonth, this.currentYear);
  }
  incrementHour(event) {
    let newHour = this.currentHour + this.stepHour;
    if (this.hourFormat == '24')
      this.currentHour = (newHour >= 24) ? (newHour - 24) : newHour;
    else if (this.hourFormat == '12')
      this.currentHour = (newHour >= 13) ? (newHour - 12) : newHour;

    this.updateTime();

    event.preventDefault();
  }

  decrementHour(event) {
    let newHour = this.currentHour - this.stepHour;
    if (this.hourFormat == '24')
      this.currentHour = (newHour < 0) ? (24 + newHour) : newHour;
    else if (this.hourFormat == '12')
      this.currentHour = (newHour <= 0) ? (12 + newHour) : newHour;

    this.updateTime();

    event.preventDefault();
  }

  incrementMinute(event) {
    let newMinute = this.currentMinute + this.stepMinute;
    this.currentMinute = (newMinute > 59) ? newMinute - 60 : newMinute;

    this.updateTime();

    event.preventDefault();
  }

  decrementMinute(event) {
    let newMinute = this.currentMinute - this.stepMinute;
    this.currentMinute = (newMinute < 0) ? 60 + newMinute : newMinute;

    this.updateTime();

    event.preventDefault();
  }

  incrementSecond(event) {
    let newSecond = this.currentSecond + this.stepSecond;
    this.currentSecond = (newSecond > 59) ? newSecond - 60 : newSecond;

    this.updateTime();

    event.preventDefault();
  }

  decrementSecond(event) {
    let newSecond = this.currentSecond - this.stepSecond;
    this.currentSecond = (newSecond < 0) ? 60 + newSecond : newSecond;

    this.updateTime();

    event.preventDefault();
  }

  updateTime() {
    this.value = this.value || new Date();
    if (this.hourFormat === '12' && this.pm && this.currentHour != 12)
      this.value.setHours(this.currentHour + 12);
    else
      this.value.setHours(this.currentHour);

    this.value.setMinutes(this.currentMinute);
    this.value.setSeconds(this.currentSecond);
    this.updateModel();
    this.onSelect.emit(this.value);
    this.updateInputfield();
  }

  toggleAMPM(event) {
    this.pm = !this.pm;
    this.updateTime();
    event.preventDefault();
  }

  onInput(event) {
    try {
      this.value = this.parseValueFromString(event.target.value);
      this.updateUI();
      this._isValid = true;
    }
    catch (err) {
      //invalid date
      this.value = null;
      this._isValid = false;
    }

    this.updateModel();
    this.updateFilledState();
  }

  parseValueFromString(text: string): Date {
    let dateValue;
    let parts: string[] = text.split(' ');

    if (this.timeOnly) {
      dateValue = new Date();
      this.populateTime(dateValue, parts[0], parts[1]);
    }
    else {
      if (this.showTime) {
        dateValue = this.parseDate(parts[0], this.dateFormat);
        this.populateTime(dateValue, parts[1], parts[2]);
      }
      else {
        dateValue = this.parseDate(text, this.dateFormat);
      }
    }

    return dateValue;
  }

  populateTime(value, timeString, ampm) {
    let time = this.parseTime(timeString);

    if (this.hourFormat == '12') {
      if (!ampm)
        throw 'Invalid Time';
      else if (ampm.toLowerCase() === 'PM' && time.hour != 12)
        value.setHours(time.hour + 12);
    }
    else {
      value.setHours(time.hour);
    }

    value.setMinutes(time.minute);
    value.setSeconds(time.second);
  }

  updateUI() {
    let val = this.value || this.defaultDate || new Date();
    this.createMonth(val.getMonth(), val.getFullYear());

    if (this.showTime || this.timeOnly) {
      let hours = val.getHours();

      if (this.hourFormat === '12') {
        if (hours >= 12) {
          this.pm = true;
          this.currentHour = (hours == 12) ? 12 : hours - 12;
        }
        else {
          this.pm = false;
          this.currentHour = (hours == 0) ? 12 : hours;
        }
      }
      else {
        this.currentHour = val.getHours();
      }

      this.currentMinute = val.getMinutes();
      this.currentSecond = val.getSeconds();
    }
  }

  onDatePickerClick(event) {
    this.closeOverlay = this.dateClick;
  }

  showOverlay(inputfield) {
    if (this.appendTo)
      this.domHandler.absolutePosition(this.overlay, inputfield);
    else
      this.domHandler.relativePosition(this.overlay, inputfield);

    this.overlayVisible = true;
    this.overlay.style.zIndex = String(++DomHandler.zindex);

    this.bindDocumentClickListener();
  }

  writeValue(value: any): void {
    this.value = value;
    if (this.value && typeof this.value === 'string') {
      this.value = this.parseValueFromString(this.value);
    }

    this.updateInputfield();
    this.updateUI();
  }

  registerOnChange(fn: Function): void {
    this.onModelChange = fn;
  }

  registerOnTouched(fn: Function): void {
    this.onModelTouched = fn;
  }

  setDisabledState(val: boolean): void {
    this.disabled = val;
  }

  // Ported from jquery-ui datepicker formatDate
  formatDate(date, format) {
    if (!date) {
      return "";
    }

    let iFormat,
      lookAhead = (match) => {
        let matches = (iFormat + 1 < format.length && format.charAt(iFormat + 1) === match);
        if (matches) {
          iFormat++;
        }
        return matches;
      },
      formatNumber = (match, value, len) => {
        let num = "" + value;
        if (lookAhead(match)) {
          while (num.length < len) {
            num = "0" + num;
          }
        }
        return num;
      },
      formatName = (match, value, shortNames, longNames) => {
        return (lookAhead(match) ? longNames[value] : shortNames[value]);
      },
      output = "",
      literal = false;

    if (date) {
      for (iFormat = 0; iFormat < format.length; iFormat++) {
        if (literal) {
          if (format.charAt(iFormat) === "'" && !lookAhead("'"))
            literal = false;
          else
            output += format.charAt(iFormat);
        }
        else {
          switch (format.charAt(iFormat)) {
            case "d":
              output += formatNumber("d", date.getDate(), 2);
              break;
            case "D":
              output += formatName("D", date.getDay(), this.locale.dayNamesShort, this.locale.dayNames);
              break;
            case "o":
              output += formatNumber("o",
                Math.round((new Date(date.getFullYear(), date.getMonth(), date.getDate()).getTime() - new Date(date.getFullYear(), 0, 0).getTime()) / 86400000), 3);
              break;
            case "m":
              output += formatNumber("m", date.getMonth() + 1, 2);
              break;
            case "M":
              output += formatName("M", date.getMonth(), this.locale.monthNamesShort, this.locale.monthNames);
              break;
            case "y":
              output += (lookAhead("y") ? date.getFullYear() :
                (date.getFullYear() % 100 < 10 ? "0" : "") + date.getFullYear() % 100);
              break;
            case "@":
              output += date.getTime();
              break;
            case "!":
              output += date.getTime() * 10000 + this.ticksTo1970;
              break;
            case "'":
              if (lookAhead("'"))
                output += "'";
              else
                literal = true;

              break;
            default:
              output += format.charAt(iFormat);
          }
        }
      }
    }
    return output;
  }

  formatTime(date) {
    if (!date) {
      return '';
    }

    let output = '';
    let hours = date.getHours();
    let minutes = date.getMinutes();
    let seconds = date.getSeconds();

    if (this.hourFormat == '12' && this.pm && hours != 12) {
      hours -= 12;
    }

    output += (hours < 10) ? '0' + hours : hours;
    output += ':';
    output += (minutes < 10) ? '0' + minutes : minutes;

    if (this.showSeconds) {
      output += ':';
      output += (seconds < 10) ? '0' + seconds : seconds;
    }

    if (this.hourFormat == '12') {
      output += this.pm ? ' PM' : ' AM';
    }

    return output;
  }

  parseTime(value) {
    let tokens: string[] = value.split(':');
    let validTokentLength = this.showSeconds ? 3 : 2;

    if (tokens.length !== validTokentLength) {
      throw "Invalid time";
    }

    let h = parseInt(tokens[0]);
    let m = parseInt(tokens[1]);
    let s = this.showSeconds ? parseInt(tokens[2]) : null;

    if (isNaN(h) || isNaN(m) || h > 23 || m > 59 || (this.hourFormat == '12' && h > 12) || (this.showSeconds && (isNaN(s) || s > 59))) {
      throw "Invalid time";
    }
    else {
      if (this.hourFormat == '12' && h !== 12) {
        h += 12;
      }

      return { hour: h, minute: m, second: s };
    }
  }

  // Ported from jquery-ui datepicker parseDate
  parseDate(value, format) {
    if (format == null || value == null) {
      throw "Invalid arguments";
    }

    value = (typeof value === "object" ? value.toString() : value + "");
    if (value === "") {
      return null;
    }

    let iFormat, dim, extra,
      iValue = 0,
      shortYearCutoff = (typeof this.shortYearCutoff !== "string" ? this.shortYearCutoff : new Date().getFullYear() % 100 + parseInt(this.shortYearCutoff, 10)),
      year = -1,
      month = -1,
      day = -1,
      doy = -1,
      literal = false,
      date,
      lookAhead = (match) => {
        let matches = (iFormat + 1 < format.length && format.charAt(iFormat + 1) === match);
        if (matches) {
          iFormat++;
        }
        return matches;
      },
      getNumber = (match) => {
        let isDoubled = lookAhead(match),
          size = (match === "@" ? 14 : (match === "!" ? 20 :
            (match === "y" && isDoubled ? 4 : (match === "o" ? 3 : 2)))),
          minSize = (match === "y" ? size : 1),
          digits = new RegExp("^\\d{" + minSize + "," + size + "}"),
          num = value.substring(iValue).match(digits);
        if (!num) {
          throw "Missing number at position " + iValue;
        }
        iValue += num[0].length;
        return parseInt(num[0], 10);
      },
      getName = (match, shortNames, longNames) => {
        let index = -1;
        let arr = lookAhead(match) ? longNames : shortNames;
        let names = [];

        for (let i = 0; i < arr.length; i++) {
          names.push([i, arr[i]]);
        }
        names.sort((a, b) => {
          return -(a[1].length - b[1].length);
        });

        for (let i = 0; i < names.length; i++) {
          let name = names[i][1];
          if (value.substr(iValue, name.length).toLowerCase() === name.toLowerCase()) {
            index = names[i][0];
            iValue += name.length;
            break;
          }
        }

        if (index !== -1) {
          return index + 1;
        } else {
          throw "Unknown name at position " + iValue;
        }
      },
      checkLiteral = () => {
        if (value.charAt(iValue) !== format.charAt(iFormat)) {
          throw "Unexpected literal at position " + iValue;
        }
        iValue++;
      };

    for (iFormat = 0; iFormat < format.length; iFormat++) {
      if (literal) {
        if (format.charAt(iFormat) === "'" && !lookAhead("'")) {
          literal = false;
        } else {
          checkLiteral();
        }
      } else {
        switch (format.charAt(iFormat)) {
          case "d":
            day = getNumber("d");
            break;
          case "D":
            getName("D", this.locale.dayNamesShort, this.locale.dayNames);
            break;
          case "o":
            doy = getNumber("o");
            break;
          case "m":
            month = getNumber("m");
            break;
          case "M":
            month = getName("M", this.locale.monthNamesShort, this.locale.monthNames);
            break;
          case "y":
            year = getNumber("y");
            break;
          case "@":
            date = new Date(getNumber("@"));
            year = date.getFullYear();
            month = date.getMonth() + 1;
            day = date.getDate();
            break;
          case "!":
            date = new Date((getNumber("!") - this.ticksTo1970) / 10000);
            year = date.getFullYear();
            month = date.getMonth() + 1;
            day = date.getDate();
            break;
          case "'":
            if (lookAhead("'")) {
              checkLiteral();
            } else {
              literal = true;
            }
            break;
          default:
            checkLiteral();
        }
      }
    }

    if (iValue < value.length) {
      extra = value.substr(iValue);
      if (!/^\s+/.test(extra)) {
        throw "Extra/unparsed characters found in date: " + extra;
      }
    }

    if (year === -1) {
      year = new Date().getFullYear();
    } else if (year < 100) {
      year += new Date().getFullYear() - new Date().getFullYear() % 100 +
        (year <= shortYearCutoff ? 0 : -100);
    }

    if (doy > -1) {
      month = 1;
      day = doy;
      do {
        dim = this.getDaysCountInMonth(year, month - 1);
        if (day <= dim) {
          break;
        }
        month++;
        day -= dim;
      } while (true);
    }

    date = this.daylightSavingAdjust(new Date(year, month - 1, day));
    if (date.getFullYear() !== year || date.getMonth() + 1 !== month || date.getDate() !== day) {
      throw "Invalid date"; // E.g. 31/02/00
    }
    return date;
  }

  daylightSavingAdjust(date) {
    if (!date) {
      return null;
    }
    date.setHours(date.getHours() > 12 ? date.getHours() + 2 : 0);
    return date;
  }

  updateFilledState() {
    this.filled = this.inputFieldValue && this.inputFieldValue != '';
  }

  bindDocumentClickListener() {
    if (!this.documentClickListener) {
      this.documentClickListener = this.renderer.listenGlobal('body', 'click', () => {
        if (this.closeOverlay) {
          this.overlayVisible = false;
        }

        this.closeOverlay = true;
        this.dateClick = false;
      });
    }
  }

  unbindDocumentClickListener() {
    if (this.documentClickListener) {
      this.documentClickListener();
    }
  }

  ngOnDestroy() {
    this.unbindDocumentClickListener();

    if (!this.inline && this.appendTo) {
      this.el.nativeElement.appendChild(this.overlay);
    }
  }

  validate(c: AbstractControl) {
    if (!this._isValid) {
      return { invalidDate: true };
    }
    return null;
  }
}

@NgModule({
  imports: [CommonModule, PibButtonModule, PibTextInputModule],
  exports: [CalendarComponent, PibButtonModule, PibTextInputModule],
  declarations: [CalendarComponent]
})
export class PibCalendarModule { }



