import { Component, OnInit } from '@angular/core';
import {HeaderStateService,HEADER_STATE} from '../services/header-state.service';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {
 statesTexts = ['hsbc-header.remplissez_formulaire','hsbc-header.signer_contrat','hsbc-header.transmettez_justificatifs','hsbc-header.activez_compte'];
 statesIcons = ['icon-fast-track',"icon-edit","icon-download","icon-accounts"];
 currentStepIcon;
 currentStepText;
 currentState : HEADER_STATE;
  constructor(private headerState: HeaderStateService) { }

  ngOnInit() {
    this.headerState.currentState().subscribe((val)=>{
       this.currentStepText = this.statesTexts[val];
       this.currentStepIcon = this.statesIcons[val];
       this.currentState = val;
    })
  }

}



