import {Component, Input, NgModule} from '@angular/core';
import {CommonModule} from "@angular/common";

@Component({
  selector: 'pib-progress-bar',
  template: `
    <div [class]="'progress-bar '+barBackgroundStyle">
        <div [class]="'bar ' + progressStyle" [ngStyle]="{'width':progress+'%'}"></div>
    </div>
  `
})
export class ProgressBar {
  @Input ('progress') progress: number = 100;
  @Input ('progressStyle') progressStyle: string = '';
  @Input ('barBackgroundStyle') barBackgroundStyle: string = '';
}


@NgModule({
  imports:[CommonModule],
  exports:[ProgressBar],
  declarations:[ProgressBar]
})
export class PibBrogressBarModule{}



