import {Component, forwardRef, Input, Output, EventEmitter, NgModule, SimpleChanges} from '@angular/core';
import {NG_VALUE_ACCESSOR, ControlValueAccessor} from "@angular/forms";
import {CommonModule} from "@angular/common";
import { TranslateService } from 'ng2-translate';

export interface SelectItem {
  label: string;
  icon : string;
  value: any;
}
export const SELECTBUTTON_VALUE_ACCESSOR: any = {
  provide: NG_VALUE_ACCESSOR,
  useExisting: forwardRef(() => SegmentControlComponent),
  multi: true
};

@Component({
  selector: 'pib-segment-control',
  template: `
               <div [ngClass]="'ui-buttonset ui-widget ui-corner-all ui-buttonset-'+ options.length" [ngStyle]="style" [class]="styleClass">
                  <div *ngFor="let option of options;" 
                       [class]="itemClass"
                        [ngClass]="{'ui-button ui-widget ui-state-default ui-button-text-only':true,
                                    'ui-state-active':isSelected(option), 
                                    'ui-state-disabled':disabled,
                                    'icon-exist': option.icon,
                                    'form__input--error':messageType == 'ERROR' && hasErrors() && (stepSubmitted),
                                    'form__input--warning':messageType == 'WARNING' && hasErrors() && (stepSubmitted)
                                  }" 
                            (click)="onItemClick($event,option)">
                      <span [class]="itemTextClass" [ngClass]="{'ui-button-text ui-c':true}">{{option.label}}</span>
                  </div>
               </div>
               <div class="form__message" [ngClass] = "{'form__message--error':messageType == 'ERROR',
                                                'form__message--warning':messageType == 'WARNING'}"
                                    *ngIf="hasErrors() && showMessage && (stepSubmitted)">
                                <i class="icon" [ngClass] = "{'icon-circle-delete' :messageType == 'ERROR',
                                          'icon-circle-error': messageType == 'WARNING'}"></i>
                                    {{messageTrad}}
              </div>
              `,
  providers: [SELECTBUTTON_VALUE_ACCESSOR]
})
export class SegmentControlComponent implements ControlValueAccessor {

  @Input() options: SelectItem[];

  @Input() multiple: boolean;

  @Input() style: any;

  @Input() styleClass: string;

  @Input() itemClass: string;

  @Input() disabled: boolean;

  @Input() itemTextClass: string;

  @Input() public message: any;

  @Input() public type : string = 'text';

  @Input() public messageType: string = 'ERROR';

  @Input() public stepSubmitted: boolean;

  @Output() onChange: EventEmitter<any> = new EventEmitter();
  @Input() public showMessage: boolean = true;
  value: any;
  private messageTrad: string;

  constructor(private _translate: TranslateService) { 
    this._translate.onLangChange.subscribe(() => {
      if(this.message){
        this.updateTrad(this.message);
      }
    })
  }

  ngOnChanges(changes: SimpleChanges){
    if(changes['message'] && changes['message'].currentValue){
      this.updateTrad(changes['message'].currentValue);
    }
  }

  updateTrad(key){
    this._translate.get(key).subscribe((value) => {
      this.messageTrad = value;
    })  
  }

  onModelChange: Function = () => {};

  onModelTouched: Function = () => {};

  writeValue(value: any) : void {
    this.value = value;
  }

  registerOnChange(fn: Function): void {
    this.onModelChange = fn;
  }

  registerOnTouched(fn: Function): void {
    this.onModelTouched = fn;
  }

  setDisabledState(val: boolean): void {
    this.disabled = val;
  }

  onItemClick(event, option: SelectItem) {
    if(this.disabled) {
      return;
    }

    if(this.multiple) {
      let itemIndex = this.findItemIndex(option);
      if(itemIndex != -1)
        this.value.splice(itemIndex, 1);
      else
        this.value.push(option.value);
    }
    else {
      this.value = option.value;
    }

    this.onModelChange(this.value);

    this.onChange.emit({
      originalEvent: event,
      value: this.value
    });
  }

  isSelected(option: SelectItem) {
    if(this.multiple)
      return this.findItemIndex(option) != -1;
    else
      return option.value == this.value;
  }

  findItemIndex(option: SelectItem) {
    let index = -1;
    if(this.value) {
      for(let i = 0; i < this.value.length; i++) {
        if(this.value[i] == option.value) {
          index = i;
          break;
        }
      }
    }
    return index;
  }

  /**
   * return true if the input has errors
   * @returns {boolean}
   */
  public hasErrors(): boolean {
    return this.message != null && this.message.trim().length > 0;
  }

}

@NgModule({
  imports: [CommonModule],
  exports: [SegmentControlComponent],
  declarations: [SegmentControlComponent]
})
export class PibSegmentControlModule { }



