import {Directive, Input, TemplateRef, OnInit, ViewContainerRef, NgModule, Component} from "@angular/core";
import {CommonModule} from "@angular/common";
@Directive({
  selector: '[pibTemplate]',
  host: {
  }
})
export class PibTemplate {

  @Input() type: string;

  @Input('pibTemplate') name: string;

  constructor(public template: TemplateRef<any>) {}

  getType(): string {
    if(this.type) {
      return this.type;
    }
    else {
      return this.name;
    }
  }
}

@Directive({
  selector: '[pibTemplateWrapper]'
})
export class TemplateWrapper implements OnInit {

  @Input() item: any;

  @Input() index: number;

  @Input('pibTemplateWrapper') templateRef: TemplateRef<any>;

  constructor(public viewContainer: ViewContainerRef) {}

  ngOnInit() {
    let view = this.viewContainer.createEmbeddedView(this.templateRef, {
      '\$implicit': this.item,
      'index': this.index
    });
  }
}
@Component({
  selector: 'pibTemplateLoader',
  template: ``
})
export class TemplateLoader {

  @Input() template: TemplateRef<any>;

  @Input() data: any;

  constructor(public viewContainer: ViewContainerRef) {}

  ngOnInit() {
    if(this.template) {
      let view = this.viewContainer.createEmbeddedView(this.template, {
        '\$implicit': this.data
      });
    }
  }
}

@NgModule({
  imports: [CommonModule],
  exports: [TemplateWrapper,TemplateLoader,PibTemplate],
  declarations: [TemplateWrapper,TemplateLoader,PibTemplate]
})
export class SharedModule { }



