import {
  Component,
  HostBinding,
  OnInit
} from '@angular/core';
import {
  Location
} from '@angular/common';
import { ActivatedRoute, Router } from '@angular/router';
import { FillDataStepsChangerService, FILL_DATA_STEPS } from '../../services/fill-data-steps-changer.service';
import {HeaderStateService,HEADER_STATE} from '../../services/header-state.service';
import {CountriesDataService} from '../../services/countries-data.service';
import {SharedModelService} from '../../services/shared-model.service';
import {UsefulDataService} from '../../services/useful-data.service';
import {TranslateService} from 'ng2-translate';
import { ScrollService } from '../../services/scroll.service';
import { RouterStateService } from '../../services/router-state.service';
import { routerTransition, holderTransition, extendTransition } from '../../routing/router.animations';

@Component({
  selector: 'app-personal-information-second-page',
  templateUrl: './personal-information-second-page.component.html',
  animations: [routerTransition(), holderTransition(), extendTransition()],
  host: {'[@routerTransition]': ''}
})
export class PersonalInformationSecondPageComponent implements OnInit {

  /**
   * @var {boolean} martialStatusClosed
   * @var {array} longNumbers
   * @var {array} nationalities
   * @var {array} countries
   * @var {array} martialStatus
   * @var {array} familySituations
   */
  martialStatusClosed = false;
  longNumbers = [];
  nationalities = [];
  countries = [];
  martialStatus = [];
  familySituations = [];
  nbNationalities = [];
  nbNationalitiesHolder = [];
  internalStep = 1;
  stepSubmitted = false;
  hideStep = false;
  holderTransitionState;
  @HostBinding('@routerTransition') routerAnimation = "next";

  /**
   * @constructor
   * Constructor of PersonalInformationSecondPageComponent class
   * @param {Location} _location location service
   * @param {Router} router router service
   * @param {HeaderStateService} _headerSteps
   * @param {TranslateService} _translate
   * @param {UsefulDataService} _usefuldata
   * @param {CountriesDataService} _countriesService
   * @param {SharedModelService} _sharedModelService
   * @param {FillDataStepsChangerService} _fillDataStepService 
   */
  constructor(private _location: Location, 
              private router: Router, 
              private route: ActivatedRoute,
              private _headerSteps:HeaderStateService,
              private _translate: TranslateService,
              private _usefuldata: UsefulDataService,
              private _countriesService: CountriesDataService,
              private _sharedModelService: SharedModelService,
              private _fillDataStepService: FillDataStepsChangerService,
              private _routerState: RouterStateService,
              private _scroll: ScrollService) { 
  //this.routerAnimation = this._routerState.lastAction;
  this.routerAnimation = this._routerState.lastAction;
  setTimeout(() => {
    this.routerAnimation = 'next'
  }, 500);
}

/**
 * Initialisations
 * @function ngOnInit
 */
  ngOnInit() {
    this.route.params.subscribe(params => {
       if(params['internalStep'] && this._sharedModelService.model.accountType === 'jointAccount'){
         this.internalStep = parseInt(params['internalStep']);
       }
       if(this._sharedModelService.model.accountType === 'jointAccount' && ( this.internalStep === 2 || (this.internalStep === 1 && this._routerState.lastAction == 'prev'))){
         this.holderTransitionState = null;  
         setTimeout(() => {
           this.hideStep = false;
           this.holderTransitionState = 'enter'  
         }, 50);  
       }
    });

    this._fillDataStepService.setCurrentStep(FILL_DATA_STEPS.INFORMATION);
    this._headerSteps.setCurrentState(HEADER_STATE.FILL_DATA);
    this._countriesService.getCountries().then((countries)=>{
      this.countries = countries;
    })
    this.updateForm();
    this._translate.onLangChange.subscribe((ev)=>{
      this.updateForm();
    })
  }

  updateForm(){
     this.familySituations = this._usefuldata.getFamilySituations(this._translate.currentLang);
      this.martialStatus = this._usefuldata.getMartialStatus(this._translate.currentLang);
      this.longNumbers = this._usefuldata.getLongNumbersName(this._translate.currentLang);
  }

  /**
   * Load internal steps for joint account (one step per holder)
   * @param step: step to load
   */
  loadInternalStep(step){
    if(step != this.internalStep){
      if(step === 2){
        this.stepSubmitted = true;
        if(this._sharedModelService.model.informations.person.validateInformationsSecondPage()){
          this._scroll.scrollTo(0, 500)
          this.holderTransitionState = 'leave';
          setTimeout(() => {
            this.hideStep = true;
            this.stepSubmitted = false;
            this.router.navigate(['personal-information-second-page/2'])
          }, 400);  
        }
      }
      else {
        this.goBack();
      }
    }
  }

  /**
   * Move to the previous step
   * @function goBack
   */
  goBack() {
    //this.routerAnimation = 'next';
    if(this.internalStep === 2){
      this._scroll.scrollTo(0, 500)
      this.holderTransitionState = 'leave';
      setTimeout(() => {
        this.hideStep = true;
        this.stepSubmitted = false;
        this._location.back();  
      }, 400);    
    }
    else {
      this._location.back();  
    }
  }

  /**
   * delete last selected nationality
   * @function deleteLastNationality
   */
  deleteLastNationality() {
   this.nbNationalities.pop();
   this._sharedModelService.model.informations.person.otherNationalities.pop();
  }

  deleteLastNationalityHolder() {
   this.nbNationalitiesHolder.pop();
   this._sharedModelService.modelHolder.informations.person.otherNationalities.pop();
  }

  /**
   * fired when a textfield is blured
   * @param event 
   */
   onKeyUp(event){
     this._sharedModelService.model.informations.person.validateInformationsSecondPage();
   }

   onKeyUpHolder(event){
     this._sharedModelService.modelHolder.informations.person.validateInformationsSecondPage();
   }

  /**
   * Move to the next step
   * @function next
   */
  next() {
    this.stepSubmitted = true;
    if(this._sharedModelService.model.informations.person.validateInformationsSecondPage()){
      this.routerAnimation = 'prev';
      setTimeout(() => {
        this._sharedModelService.mustSaveData.next(true);

        if(this._sharedModelService.model.accountType === 'individualAccount'){
          this.stepSubmitted = false;
          this.router.navigate(['/coordonees']);
        }
        else if(this.internalStep === 1) {
          this._scroll.scrollTo(0, 500)
          this.holderTransitionState = 'leave';
          setTimeout(() => {
            this.hideStep = true;
            this.stepSubmitted = false;
            this.router.navigate(['personal-information-second-page/2'])
          }, 400);
        }
        else if (this._sharedModelService.modelHolder.informations.person.validateInformationsSecondPage()){
          this.router.navigate(['/coordonees']);
        }
        else {
          setTimeout(() => {
            this._scroll.scrollToFirstBadField()  
          }, 50);
        }
      }, 50);
      
    }
    else {
      setTimeout(() => {
        this._scroll.scrollToFirstBadField()  
      }, 50);
    }
  }

  /**
   * Add anothernationality
   * @function AddOtherNationality
   */
  AddOtherNationality() {
    this.nbNationalities.push(Math.random())
    this._sharedModelService.model.informations.person.otherNationalities.push('FR');
    this.onKeyUp(null);
  }

  AddOtherNationalityHolder() {
    this.nbNationalitiesHolder.push(Math.random())
    this._sharedModelService.modelHolder.informations.person.otherNationalities.push('FR');
    this.onKeyUpHolder(null);
  }
}




