import {NgModule,Component,OnInit,Input,Output,EventEmitter,TemplateRef,AfterContentInit,ContentChildren,QueryList} from '@angular/core';
import {CommonModule} from '@angular/common';
import {DomSanitizer} from '@angular/platform-browser';
import {PibTemplate,SharedModule} from '../common/shared';
import {Message} from "../common/api";
import {PibButtonModule} from "../directives/button.directive";

@Component({
  selector: 'pib-fileUpload',
  template: `
       <div [ngClass]="'ui-fileupload ui-widget'" [ngStyle]="style" [class]="styleClass">
            <div [ngClass]="{'form__file':true,'ui-fileupload-highlight':dragHighlight,'form__file--active':hasFiles()}" 
                 (click)="onChooseClick($event, fileinput)"
                 (dragenter)="onDragEnter($event)" (dragover)="onDragOver($event)" (dragleave)="onDragLeave($event)" (drop)="onDrop($event)">
                 <input #fileinput type="file" [name]="name" (change)="onFileSelect($event)" [multiple]="false" [accept]="accept" [disabled]="disabled"/>
                 <div *ngIf="!uploadTemplate">
                     <div class="form__file__content" *ngIf="!hasFiles()">
                       <i class="icon icon-upload"></i> {{uploadLabel}}
                     </div>
                 </div>
                 <div *ngIf="!contentTemplate">
                     <div class="form__file__content" *ngIf="hasFiles()">
                       <i class="icon icon-circle-confirmation"></i> {{file.name}}
                     </div>
                 </div>
                 <template [ngTemplateOutlet]="uploadTemplate" [ngOutletContext]="{file: file}"></template>
                 <template [ngTemplateOutlet]="contentTemplate" [ngOutletContext]="{file: file}"></template>
            </div>
        </div>
    `
})
export class FileUpload implements OnInit,AfterContentInit {

  @Input() name: string;

  @Input() url: string;

  @Input() accept: string;

  @Input() disabled: boolean;

  @Input() uploadLabel: string;

  @Input() auto: boolean;

  @Input() maxFileSize: number;

  @Input() invalidFileSizeMessageSummary: string = '{0}: Invalid file size, ';

  @Input() invalidFileSizeMessageDetail: string = 'maximum upload size is {0}.';

  @Input() style: string;

  @Input() styleClass: string;

  @Output() onBeforeUpload: EventEmitter<any> = new EventEmitter();

  @Output() onBeforeSend: EventEmitter<any> = new EventEmitter();

  @Output() onUpload: EventEmitter<any> = new EventEmitter();

  @Output() onError: EventEmitter<any> = new EventEmitter();

  @Output() onSelect: EventEmitter<any> = new EventEmitter();

  @ContentChildren(PibTemplate) templates: QueryList<any>;

  public file: File;

  public dragHighlight: boolean;

  public msgs: Message[];

  public contentTemplate: TemplateRef<any>;

  public uploadTemplate: TemplateRef<any>;

  constructor(private sanitizer: DomSanitizer){}

  ngOnInit() {
    this.file = null;
  }

  ngAfterContentInit():void {
    this.templates.forEach((item) => {
      switch(item.getType()) {
        case 'content':
          this.contentTemplate = item.template;
          break;
        case 'upload':
          this.uploadTemplate = item.template;
          break;
      }
    });
  }

  onChooseClick(event, fileInput) {
    fileInput.value = null;
    fileInput.click();
  }

  onFileSelect(event) {
    this.msgs = [];

    let files = event.dataTransfer ? event.dataTransfer.files : event.target.files;
    if(files && files.length > 0){
      let file = files[0];
      if(this.validate(file)) {
        if(this.isImage(file)) {
          file.objectURL = this.sanitizer.bypassSecurityTrustUrl((window.URL.createObjectURL(file)));
        }

        this.file = file
      }
    }

    this.onSelect.emit({originalEvent: event, file: this.file});

    if(this.hasFiles() && this.auto) {
      console.log(this.auto)
      this.upload();
    }
  }

  validate(file: File): boolean {
    if(this.maxFileSize  && file.size > this.maxFileSize) {
      this.msgs.push({
        severity: 'error',
        summary: this.invalidFileSizeMessageSummary.replace('{0}', file.name),
        detail: this.invalidFileSizeMessageDetail.replace('{0}', this.formatSize(this.maxFileSize))
      });
      return false;
    }

    return true;
  }

  isImage(file: File): boolean {
    return /^image\//.test(file.type);
  }

  onImageLoad(img: any) {
    window.URL.revokeObjectURL(img.src);
  }

  upload() {
    this.msgs = [];
    let xhr = new XMLHttpRequest(),
      formData = new FormData();

    this.onBeforeUpload.emit({
      'xhr': xhr,
      'formData': formData
    });
    formData.append(this.name, this.file, this.file.name);
    xhr.onreadystatechange = () => {
      if(xhr.readyState == 4) {
        if(xhr.status >= 200 && xhr.status < 300)
          this.onUpload.emit({xhr: xhr, files: this.file});
        else
          this.onError.emit({xhr: xhr, files: this.file});
      }
    };

    xhr.open('POST', this.url, true);

    this.onBeforeSend.emit({
      'xhr': xhr,
      'formData': formData
    });

    xhr.send(formData);
  }

  hasFiles(): boolean {
    return this.file != null
  }

  onDragEnter(e) {
    if(!this.disabled) {
      e.stopPropagation();
      e.preventDefault();
    }
  }

  onDragOver(e) {
    if(!this.disabled) {
      this.dragHighlight = true;
      e.stopPropagation();
      e.preventDefault();
    }
  }

  onDragLeave(e) {
    if(!this.disabled) {
      this.dragHighlight = false;
    }
  }

  onDrop(e) {
    if(!this.disabled) {
      this.dragHighlight = false;
      e.stopPropagation();
      e.preventDefault();

      this.onFileSelect(e);
    }
  }

  formatSize(bytes) {
    if(bytes == 0) {
      return '0 B';
    }
    let k = 1000,
      dm = 3,
      sizes = ['B', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'],
      i = Math.floor(Math.log(bytes) / Math.log(k));

    return parseFloat((bytes / Math.pow(k, i)).toFixed(dm)) + ' ' + sizes[i];
  }
}

@NgModule({
  imports: [CommonModule,SharedModule,PibButtonModule],
  exports: [FileUpload,SharedModule,PibButtonModule],
  declarations: [FileUpload]
})
export class PibFileUploadModule { }






