
import {NgModule,Component,Input} from '@angular/core';
import {CommonModule} from '@angular/common';


@Component({
  selector: 'pib-steps',
  template: `<div [class]="styleClass" [ngStyle]="style">
              <span *ngIf="hideStepsTextOnMobile" class="steps__title" [ngClass]="{'is-displayed-mobile':hideStepsTextOnMobile}">{{title}} {{activeIndex+1}}/{{model.length}}</span>
              <ul role="tablist">
                  <li *ngFor="let item of model;let i = index"
                       [ngStyle]="{'width':(100/model.length) + '%'}"
                      [class] = "itemClass"
                      [ngClass]="{'highlight':(i <= activeIndex),'disabled':(i > activeIndex)}">
                      <a class="steps__title__link" [ngClass]="{'is-hidden-mobile':hideStepsTextOnMobile}">
                          <span class="steps__title">{{item}}</span>
                      </a>
                   <div class="steps__bar" 
                        [ngStyle]="{'width': wid + '%'}"
                      ></div>


                  </li>
              </ul>
          </div>
          `,
          styles: [`
          .steps--underlined__item.highlight .steps__bar{
                  background: #DA0010;
                    height: 100%;
                    min-width: 0%;
                    max-width: 100%;
                    height: 3px;
                    bottom: -3px;
                    position: absolute;
                    left: 0;
                    left: 0;
                    -webkit-transition: width 0.6s ease;
                    -moz-transition: width 0.6s ease;
                    -o-transition: width 0.6s ease;
                    transition: width 0.6s ease;
          }
          `]
})

export class Steps {


  @Input() activeIndex: number = 0;//current active index

  @Input() model: string[] = [];//steps items

  @Input() style: string ='';//steps style

  @Input() styleClass: string='';//steps class

  @Input() itemClass: string = ''; //steps item class

  @Input() hideStepsTextOnMobile: boolean = false;

  @Input() title : string;

  @Input() wid : number = 0;

}

@NgModule({
  imports: [CommonModule],
  exports: [Steps],
  declarations: [Steps]
})
export class PibStepsModule { }



