import { Injectable } from '@angular/core';
import {Model} from '../model/Model';
import {BehaviorSubject} from 'rxjs/BehaviorSubject';
import {LocalDataService} from './local-data.service';

@Injectable()
export class SharedModelService {
 //private _model = new Model()
 private _model : Model;
 private _modelHolder : Model;
 public mustSaveData : BehaviorSubject<boolean>;

  /**
   * @constructor
   * Constuctor of AccountTypeComponent class
   * @param {LocalDataService} _localData service
   */
  constructor(private _localData:LocalDataService) {
    
    this.initModel();

    this.mustSaveData = new BehaviorSubject(false);
    this.mustSaveData.subscribe((val) => {

      // Check if account holder
      if(this._model.accountType === 'jointAccount' && !this._modelHolder){
        this.initModelHolder();
      }

      // Check for save local data
      if(val){
        this._localData.setDataModel(this._model);

        if(this._model.accountType === 'jointAccount' && this._modelHolder){
          this._localData.setDataModelHolder(this._modelHolder);          
        }
        this.mustSaveData.next(false);  
      }

    })

    console.log('Initialized model', this._model);

  }

  private initModel(): void{
    // Check if local data
    let _dataModel = this._localData.getDataModel() as Model;

    if(_dataModel){
      this._model = new Model(_dataModel);
    }
    else{
      this._model = new Model();
    }
  }

  private initModelHolder(): void{
    console.log('=> Creating joint account');
    let _dataModelHolder = this._localData.getDataModelHolder() as Model;

    // Check if local data
    if(_dataModelHolder){
      this._modelHolder = new Model(_dataModelHolder);
    }
    else {
      this._modelHolder = new Model();
    }
  }

  get model(){
    return this._model
  }

  get modelHolder(){
    return this._modelHolder
  }


}



