import {
  Component, NgModule, ViewChild, ElementRef, animate, transition, style, state, trigger,
  Input, QueryList, ContentChildren, AfterContentInit, OnInit
} from "@angular/core";
import { EventEmitter } from "@angular/core";
import { TemplateRef } from "@angular/core";
import {CommonModule} from "@angular/common";
import {PibTemplate} from "../common/shared";

@Component({
  selector: "pib-generic-dropdown",
  inputs: [ "items", "value", "placeholder" ],
  host: {
    '(document:click)': 'onClick($event)',
  },
  outputs: [ "valueChange" ],

  // Query for the template being provided by the calling context.

  template:
    `
		<div (click)="toggleItems($event)" [ngClass]="{'dropdown':true,'active':isShowingItems}" [class]="containerClass"  #container>
		   <template [ngIf]="headerTemplate != null">
           <template
            [ngTemplateOutlet]="headerTemplate"
            [ngOutletContext]="{ item: value, index: -1 }">
          </template>
        </template>
        <template  [ngIf]="headerTemplate == null && itemTemplate != null">
          <template
            [ngTemplateOutlet]="itemTemplate"
            [ngOutletContext]="{ item: value, index: -1 }">
          </template>
        </template>
        <template  [ngIf]="headerTemplate == null && itemTemplate == null">
         <label>{{placeholder}}</label>
        </template>
		<ul class="dropdown__menu" [@menuState]="isShowingItems ? 'visible' : 'hidden'">
			<li class="dropdown__item"
				*ngFor="let item of items ; let index = index ;" 
				(click)="selectItem( $event,item )"
				[ngClass] = "{'active':item == value}">
				<a class="dropdown__link">
				<template [ngIf] = "itemTemplate != null">
          <template 
              [ngTemplateOutlet]="itemTemplate"
              [ngOutletContext]="{ item: item, index: index }">
            </template>
         </template>
         <template [ngIf] = "itemTemplate == null">
           {{item}}
         </template>
				</a>
			</li>
		</ul>
	 </div>
	`,
  animations: [
    trigger('menuState', [
      state('hidden', style({
        height: 0,
        overflow:'hidden'
      })),
      state('visible', style({
        height: '*',
        overflow:'*'
      })),
      transition('visible => hidden', animate('400ms ease-in')),
      transition('hidden => visible', animate('400ms ease-out'))
    ])
  ],
})
export class GenericDropdown implements AfterContentInit,OnInit{


  public isShowingItems: boolean;

  public items: any[];

  public placeholder: string;

  public value: any;

  valueChange: EventEmitter<any>;

  @ContentChildren(PibTemplate) templates: QueryList<any>;

  public headerTemplate: TemplateRef<any>;

  public itemTemplate: TemplateRef<any>;

  @Input() containerClass = '';

  @ViewChild('container') container: ElementRef;
  constructor() {

    this.isShowingItems = false;
    this.valueChange = new EventEmitter();

  }
  ngOnInit(){
    if(!this.value && this.items.length > 0){
      this.value = this.items[0];
    }
  }
  public hideItems() : void {
    this.isShowingItems = false;
  }

  public selectItem(event,item: any ) : void {

    this.hideItems();
    this.valueChange.emit( item );
    event.stopPropagation();

  }
  ngAfterContentInit():void {
    this.templates.forEach((item) => {
      switch(item.getType()) {
        case 'header':
          this.headerTemplate = item.template;
          break;
        case 'item':
          this.itemTemplate = item.template;
          break;
      }
    });
  }
  public showItems() : void {
    this.isShowingItems = true;
  }

  public toggleItems() : void {
    this.isShowingItems
      ? this.hideItems()
      : this.showItems()
    ;
  }
  onClick(event) {
    if (!this.container.nativeElement.contains(event.target)){
      this.hideItems()
    }

  }
}
@NgModule({
  imports: [CommonModule],
  exports: [GenericDropdown],
  declarations: [GenericDropdown]
})
export class PibGenericDropdownModule { }



