import {
  Component, OnInit, NgModule, Input, animate, transition, style, state, trigger, Output,
  EventEmitter, QueryList, ContentChildren
} from '@angular/core';
import {CommonModule} from "@angular/common";
import {RouterModule,Router, NavigationEnd, RouterLinkWithHref, RouterLink} from "@angular/router";
import {Subscription} from "rxjs";


/**
 * side bar
 */
@Component({
  selector: 'pib-sidebar',
  template: `
   <ul [ngClass] ="{'sidebar':true}" [class]="containerClass" >
     <ng-content></ng-content>
   </ul>
  `
})
export class SideBarComponent {
  @Input() containerClass = ''
  public items: SideBarItemComponent[] = [];
  constructor() { }
  addItem(item: SideBarItemComponent) {
    this.items.push(item);
  }
  closeOther(item: SideBarItemComponent){
    for(var i = 0; i< this.items.length; i++){
      if(this.items[i] != item){
        this.items[i].close()
      }
    }
  }
  indexOf(sideBarItem: SideBarItemComponent){
    return this.items.indexOf(sideBarItem);
  }

}


/**
 * side bar item
 */

@Component({
  selector: 'pib-sidebar-item',
  template: `
   <li class="sidebar__item" [ngClass]="{'active':selected}" (click)="onClick($event)">
        <span *ngIf="title" class="sidebar__item__title">{{title}}</span>
        <ul class="sidebar__subnav" [@sideBarItemContent]="selected ? 'visible' : 'hidden'">        
             <ng-content></ng-content>
        </ul>
   </li>
  `,
  animations: [
    trigger('sideBarItemContent', [
      state('hidden', style({
        height: '0px'
      })),
      state('visible', style({
        height: '*'
      })),
      transition('visible => hidden', animate('400ms ease-out')),
      transition('hidden => visible', animate('400ms ease-in'))
    ])
  ]
})
export class SideBarItemComponent{
  @Input('title') title : string;
  @Input('active') selected: boolean;
  @Output('onOpen') onOpen: EventEmitter<any> = new EventEmitter();
  @ContentChildren(RouterLink, {descendants: true}) links: QueryList<RouterLink>;
  @ContentChildren(RouterLinkWithHref, {descendants: true})
  linksWithHrefs: QueryList<RouterLinkWithHref>;
  private subscription: Subscription;
  private subItems: SideBarSubItemComponent[] = [];
  constructor(public sidebar: SideBarComponent,private router:Router) {
    this.subscription = router.events.subscribe(s => {
      if (s instanceof NavigationEnd) {
        this.update();
      }
    });
    this.sidebar.addItem(this);
  }
  addSubItem(subItem: SideBarSubItemComponent){
    this.subItems.push(subItem)
  }
  onClick(e){
    if(!this.selected){
      this.selected = true;
      let index = this.sidebar.indexOf(this);
      if(this.subItems.length > 0){
        this.subItems[0].open(null);
      }
      this.onOpen.emit({index:index,event:e})
      this.sidebar.closeOther(this);
    }
  }
  closeOther(item: SideBarSubItemComponent){
    this.subItems.forEach((subItem:SideBarSubItemComponent)=>{
      if(item != subItem){
        subItem.selected = false
      }
    })
  }
  indexOf(item: SideBarSubItemComponent){
    return this.subItems.indexOf(item);
  }
  close(){
    this.selected = false;
    this.subItems.forEach((subItem:SideBarSubItemComponent)=>{
      subItem.selected = false;
    })
  }
  private update(): void {
    if (!this.links || !this.linksWithHrefs || !this.router.navigated) return;
    const hasActiveLinks = this.hasActiveLinks();
    // react only when status has changed to prevent unnecessary dom updates
    if (this.selected !== hasActiveLinks) {
      this.selected = hasActiveLinks;

    }
  }

  private isLinkActive(router: Router): (link: (RouterLink|RouterLinkWithHref)) => boolean {
    return (link: RouterLink | RouterLinkWithHref) =>
      router.isActive(link.urlTree, false);
  }

  private hasActiveLinks(): boolean {
    return this.links.some(this.isLinkActive(this.router)) ||
      this.linksWithHrefs.some(this.isLinkActive(this.router));
  }
}


/**
 * side bar subitem
 */


@Component({
  selector: 'pib-sidebar-subitem',
  template: `
   <li class="sidebar__subitem" [ngClass]="{'active': selected}" (click)="onClick($event)">
      <ng-content></ng-content>
   </li>
  `
})
export class SideBarSubItemComponent {
  @Input('active') selected: boolean;
  @Output('onSelect') onSelect: EventEmitter<any> = new EventEmitter();
  @ContentChildren(RouterLink, {descendants: true}) links: QueryList<RouterLink>;
  @ContentChildren(RouterLinkWithHref, {descendants: true})
  linksWithHrefs: QueryList<RouterLinkWithHref>;
  private subscription: Subscription;
  constructor(private sideBarItem: SideBarItemComponent,private router:Router) {
    this.subscription = router.events.subscribe(s => {
      if (s instanceof NavigationEnd) {
        this.update();
      }
    });
    sideBarItem.addSubItem(this);
  }
  onClick(e){
   this.open(e);
    this.sideBarItem.closeOther(this);
    e.stopPropagation();
  }
  open(e){
    this.selected = true
    let index = this.sideBarItem.indexOf(this);
    this.onSelect.emit({index: index, event:e});
  }
  private update(): void {
    if (!this.links || !this.linksWithHrefs || !this.router.navigated) return;
    const hasActiveLinks = this.hasActiveLinks();
    // react only when status has changed to prevent unnecessary dom updates
    if (this.selected !== hasActiveLinks) {
      this.selected = hasActiveLinks;

    }
  }

  private isLinkActive(router: Router): (link: (RouterLink|RouterLinkWithHref)) => boolean {
    return (link: RouterLink | RouterLinkWithHref) =>{
      return router.isActive(link.urlTree, false);
    }

  }

  private hasActiveLinks(): boolean {
    return this.links.some(this.isLinkActive(this.router)) ||
      this.linksWithHrefs.some(this.isLinkActive(this.router));
  }
}


@NgModule({
  imports: [CommonModule,RouterModule],
  exports: [SideBarComponent,SideBarItemComponent,SideBarSubItemComponent],
  declarations: [SideBarComponent,SideBarItemComponent,SideBarSubItemComponent]
})
export class PibSideBarModule { }



