import { Component, ElementRef, OnInit, ViewChild, HostBinding } from '@angular/core';
import { MenuItem, SelectItem } from '../../common/api';
import { TranslateService } from 'ng2-translate';
import { Router, ActivatedRoute } from '@angular/router';
import { Location, PlatformLocation } from '@angular/common';
import { FillDataStepsChangerService, FILL_DATA_STEPS } from '../../services/fill-data-steps-changer.service';
import { HeaderStateService, HEADER_STATE } from '../../services/header-state.service';
import { SharedModelService } from '../../services/shared-model.service';
import { CountriesDataService } from '../../services/countries-data.service';
import { RouterStateService } from '../../services/router-state.service';
import { ScrollService } from '../../services/scroll.service';
import {Modal} from '../../components/modal.component';
import { routerTransition, holderTransition } from '../../routing/router.animations';

@Component({
  selector: 'app-personal-informations',
  templateUrl: './personal-informations.component.html',
  animations: [routerTransition(), holderTransition()],
  host: {'[@routerTransition]': ''},
  styles: []
})
export class PersonalInformationsComponent implements OnInit {

  /**
 * @var {array} civilities
 * @var {array} cities
 * @var {string} selectedCity
 * @var {string} selectedCivility
 */
  currentLocal = null;
  civilities = [{ label: 'Madame', value: 'md' }, { label: 'Monsieur', value: 'mo' }]
  cities = []
  selectedCity: string = 'FR';
  selectedCivility = 'md'
  birthDay = ""
  birthMonth = ""
  birthYear = ""
  birthDayHolder = ""
  birthMonthHolder = ""
  birthYearHolder = ""
  stepSubmitted = false;
  internalStep = 1;
  hideStep = false;
  holderTransitionState;
  @HostBinding('@routerTransition') routerAnimation;

  @ViewChild('inputBirthDay') inputBirthDay: ElementRef;
  @ViewChild('inputBirthMonth') inputBirthMonth: ElementRef;
  @ViewChild('inputBirthYear') inputBirthYear: ElementRef;
  @ViewChild('modalMinor') modalMinor: Modal;

  /**
   * @constructor
   * Constructor of PersonalInformationsComponent class
   * @param {TranslateService} translate
   * @param {Location} _location location service
   * @param {Router} router router service
   * @param {HeaderStateService} _headerSteps
   * @param {SharedModelService} _sharedModelService Model service
   * @param {FillDataStepsChangerService} _fillDataStepService 
   */
  constructor(private _translate: TranslateService, private router: Router, private route: ActivatedRoute,
    private _location: Location, private _platformLocation: PlatformLocation, private _headerSteps: HeaderStateService,
    private _sharedModelService: SharedModelService,
    private _countriesService: CountriesDataService,
    private _fillDataStepService: FillDataStepsChangerService,
    private _routerState: RouterStateService,
    private _scroll: ScrollService) {
    this.routerAnimation = this._routerState.lastAction;
    setTimeout(() => {
      this.routerAnimation = 'next'
    }, 500);

  }

  /**
   * Initialisations
   * @function ngOnInit
   */
  ngOnInit() {

    this.route.params.subscribe(params => {
       if(params['internalStep'] && this._sharedModelService.model.accountType === 'jointAccount'){
         this.internalStep = parseInt(params['internalStep']);
       }
       if(this._sharedModelService.model.accountType === 'jointAccount' && ( this.internalStep === 2 || (this.internalStep === 1 && this._routerState.lastAction == 'prev'))){
         this.holderTransitionState = null;  
         setTimeout(() => {
           this.hideStep = false;
           this.holderTransitionState = 'enter'  
         }, 50);  
       }
    });

    this._fillDataStepService.setCurrentStep(FILL_DATA_STEPS.INFORMATION);
    this._headerSteps.setCurrentState(HEADER_STATE.FILL_DATA);
    this._countriesService.getData().then((data) => {
      this.cities = data;
    })
    this.updateForm()
    this._translate.onLangChange.subscribe((ev) => {
      this.updateForm()
    })
  }

  /**
  * Init form with specific values
  * @function updateForm
  */
  updateForm() {
    this.currentLocal = this._countriesService.getLocale(this._translate.currentLang);
    this._translate.get('hsbc-main.madame').subscribe((value) => {
      this.civilities[0].label = value
    })
    this._translate.get('hsbc-main.monsieur').subscribe((value) => {
      this.civilities[1].label = value
    })
    if(this._sharedModelService.model.informations.person.birthDate){
      this.onSelect(this._sharedModelService.model.informations.person.birthDate);  
    }
    if(this._sharedModelService.model.accountType === 'jointAccount' && this._sharedModelService.modelHolder.informations.person.birthDate){
      this.onSelectHolder(this._sharedModelService.modelHolder.informations.person.birthDate);
    } 
  }

  /**
   * Load internal steps for joint account (one step per holder)
   * @param step: step to load
   */
  loadInternalStep(step){
    if(step != this.internalStep){
      if(step === 2){
        this.stepSubmitted = true;
        if(this._sharedModelService.model.informations.person.validate()){
          this._scroll.scrollTo(0, 500)
          this.holderTransitionState = 'leave';
          setTimeout(() => {
            this.hideStep = true;
            this.stepSubmitted = false;
            this.router.navigate(['../personal-information/2'])
          }, 400);  
        }
      }
      else {
        this.goBack();
      }
    }
  }

  /**
   * convert number >= 0 and < 100 to tow digit string
   * @param val 
   */
  towDigit(val: number) {
    if (val < 10) {
      return '0' + val
    }
    return val.toString()
  }

  /**
   * fired when a date is selected from the calendar
   * @param date 
   */
  onSelect(date) {
    this.birthDay = this.towDigit(date.getDate())
    this.birthMonth = this.towDigit(date.getMonth() + 1)
    this.birthYear = date.getFullYear().toString()
    this.onTfChange(null, 'model');
  }
  
  /**
   * fired when a date is selected from the calendar
   * @param date 
   */
  onSelectHolder(date) {
    this.birthDayHolder = this.towDigit(date.getDate())
    this.birthMonthHolder = this.towDigit(date.getMonth() + 1)
    this.birthYearHolder = date.getFullYear().toString()
    this.onTfChange(null, 'modelHolder');
  }

  /**
   * fired when a textfield content changed, textfield is one of [birthDay,birthMonth,birthYear]
   * @param event 
   */
  onTfChange(event, model) {
    let birthDay, birthMonth, birthYear;
    if(model === 'model'){
      birthDay = this.birthDay,
      birthMonth = this.birthMonth,
      birthYear = this.birthYear;
    }
    else if(model === 'modelHolder'){
      birthDay = this.birthDayHolder,
      birthMonth = this.birthMonthHolder,
      birthYear = this.birthYearHolder;
    }

    try {
      if(birthDay && birthDay.trim().length === 2 && 
         birthMonth && birthMonth.trim().length === 2 &&
         birthYear && birthYear.trim().length === 4){
            let dateStr = birthMonth + '/' + birthDay + '/' + birthYear
            let date = new Date(dateStr)
            if (this._sharedModelService[model].informations.person.validateDate(date)) {
                if(this._sharedModelService[model].informations.person.getAge(date) < 18){
                  this.modalMinor._isOpened = true;
                }
                else {
                  this._sharedModelService[model].informations.person.birthDate = date;
                  this._sharedModelService[model].informations.person.birthDateError = null;
                }     
            }
            else {
              console.log('=> ERR DATE');
              if (!this._sharedModelService[model].informations.person.birthDate) {
                this._translate.get('hsbc-main.error_field_required').subscribe((value) => {
                  this._sharedModelService[model].informations.person.birthDateError = value;
                })
              } else {
                  this._translate.get('hsbc-main.error_field_invalid').subscribe((value) => {
                    this._sharedModelService[model].informations.person.birthDateError = value;
                  })
              }
            }
         }
         else {
           this._translate.get('hsbc-main.error_field_required').subscribe((value) => {
              this._sharedModelService[model].informations.person.birthDateError = value;
            })
         }
    } catch (e) {
      console.log('Error date', e);
    }
  }

  /**
   * fired when a textfield is blured
   * @param event 
   */
   onKeyUp(event){
     this._sharedModelService.model.informations.person.validate();
   }

   onKeyUpHolder(event){
     this._sharedModelService.modelHolder.informations.person.validate();
   }

  /**
   * Move to the previous step
   * @function goBack
   */
  goBack() {

    if(this.internalStep === 2){
      this._scroll.scrollTo(0, 500)
      this.holderTransitionState = 'leave';
      setTimeout(() => {
        this.hideStep = true;
        this.stepSubmitted = false;
        this._location.back();  
      }, 400);    
    }
    else {
      this._location.back();  
    }
  
    
  }

  /**
   * Validate user and move to the next step
   * @function next
   */
  next() {
    this.stepSubmitted = true;
    let valid = this._sharedModelService.model.informations.person.validate();
    if (valid) {
      this.routerAnimation = 'prev';
      setTimeout(() => {
        this._sharedModelService.mustSaveData.next(true);
        if(this._sharedModelService.model.accountType === 'individualAccount'){
          this.stepSubmitted = false;
          this.router.navigate(['personal-information-second-page']);  
        }
        else if(this.internalStep === 1) {
          this._scroll.scrollTo(0, 500)
          this.holderTransitionState = 'leave';
          setTimeout(() => {
            this.hideStep = true;
            this.stepSubmitted = false;
            this.router.navigate(['../personal-information/2'])
          }, 400);
        }
        else if (this._sharedModelService.modelHolder.informations.person.validate()){
          this.stepSubmitted = false;
          this.router.navigate(['personal-information-second-page/1']);  
        }
        else {
          setTimeout(() => {
            this._scroll.scrollToFirstBadField()  
          }, 50);
        }
      }, 50)
    }
    else {
      setTimeout(() => {
        this._scroll.scrollToFirstBadField()  
      }, 50);
    }
  }

}



