import { Component, HostBinding, OnInit } from '@angular/core';
import { TranslateService } from 'ng2-translate';
import { Router } from '@angular/router';
import { Location } from '@angular/common';
import { FillDataStepsChangerService, FILL_DATA_STEPS } from '../../services/fill-data-steps-changer.service';
import { HeaderStateService, HEADER_STATE } from '../../services/header-state.service';
import { SharedModelService } from '../../services/shared-model.service';
import { RouterStateService } from '../../services/router-state.service';
import { routerTransition } from '../../routing/router.animations';
import { CustomPolyfills } from '../../../custom-polyfills'

@Component({
  selector: 'app-recap-validation',
  templateUrl: './recap-validation.component.html',
  styleUrls: ['./recap-validation.component.css'],
  animations: [routerTransition()],
  host: {'[@routerTransition]': ''}
})
export class RecapValidationComponent implements OnInit {

    /**
   * @var {array} numbersAsLetters
   * @var {string} expanded
   */
  numbersAsLetters = ['Seconde', 'Troisime'];
  expanded = null;
  currentDate = new Date();
  @HostBinding('@routerTransition') routerAnimation = "next";

  /**
   * Constructor of RecapValidationComponent class
   * @constructor
   * @param {TranslateService} translate
   * @param {Location} _location location service
   * @param {Router} router router service
   * @param {HeaderStateService} _headerSteps
   * @param {SharedModelService} _sharedModelService Model service
   * @param {FillDataStepsChangerService} _fillDataStepService 
   */
  constructor(translate: TranslateService, private router: Router,
    private _location: Location, private _headerSteps: HeaderStateService,
    private _sharedModelService: SharedModelService,
    private _fillDataStepService: FillDataStepsChangerService,
    private _routerState: RouterStateService) { 

    this.routerAnimation = this._routerState.lastAction;
    setTimeout(() => {
      this.routerAnimation = 'next'
    }, 500);

  }

/**
 * Initialisations
 * @function ngOnInit
 */
  ngOnInit() {
    this._fillDataStepService.setCurrentStep(FILL_DATA_STEPS.VALIDATION);
    this._headerSteps.setCurrentState(HEADER_STATE.FILL_DATA);
  }

/**
 * Expand drop down list
 * @function expand
 * @param {string} detail Indicates the expanded list among all list
 */
  expand(detail) {
    if (this.expanded == detail) {
      this.expanded = null;
    } else {
      this.expanded = detail;
    }
  }

  /**
   * Move to the previous step
   * @function goBack
   */
  goBack() {
    this._location.back();
  }

  /**
   * Move to the next step
   * @function next
   */
  next() {
    this.routerAnimation = 'prev';

    let customEvent = CustomPolyfills.createNewEvent('submit-ng')
    console.log('-> Event :', customEvent);
    window.dispatchEvent( customEvent );

    // setTimeout(() => {
    //   this.router.navigate(['opening-account-information']);
    // },50)
  }

}



