import { Component, OnInit } from '@angular/core';
import {TranslateService, LangChangeEvent} from "ng2-translate";
import {LocalDataService} from '../services/local-data.service';
@Component({
  selector: 'app-footer',
  templateUrl: './footer.component.html',
  styleUrls: ['./footer.component.css']
})
export class FooterComponent implements OnInit {
activeLang = 'fr'

  constructor(private translateService:TranslateService, private localData: LocalDataService) {
    this.activeLang = translateService.currentLang
  }

  ngOnInit() {
    this.translateService.onLangChange.subscribe((event: LangChangeEvent) => {
      // do something
      this.activeLang = this.translateService.currentLang
    });

  }
  onSelectLanguage(language:string){
    if(language == 'fr' || language == 'en'){
      this.localData.setDataLang(language);
      this.translateService.use(language);
    }
  }
}



