import {NgModule, Component, Input, Output, EventEmitter, forwardRef, TemplateRef, ContentChild} from '@angular/core';
import {CommonModule} from '@angular/common';
import {NG_VALUE_ACCESSOR, ControlValueAccessor} from '@angular/forms';

export const CHECKBOX_VALUE_ACCESSOR: any = {
  provide: NG_VALUE_ACCESSOR,
  useExisting: forwardRef(() => Checkbox),
  multi: true
};

@Component({
  selector: 'pib-checkbox',
  queries: {
    itemTemplate: new ContentChild( TemplateRef )
  },
  template: `<div [class]="containerClass" (click)="onClick($event,cb,true)">
               <input type="checkbox" #cb [name]="name" [value]="value" (click)="$event.stopPropagation()" [checked]="checked" (focus)="onFocus($event)" (blur)="onBlur($event)">
               <label [class] = "labelClass" [ngStyle] = "labelStyle" 
                      (click)="onClick($event,cb,true)">{{itemTemplate? '':label}}
                 <template 
                        [ngTemplateOutlet]="itemTemplate"
                        [ngOutletContext]="{ value: value, label: label }">
					       </template>
               </label>
             </div>         
            `,
  providers: [CHECKBOX_VALUE_ACCESSOR]
})
export class Checkbox implements ControlValueAccessor {

  @Input() value: any;

  @Input() name: string;

  @Input() labelClass: string = '';

  @Input() disabled: boolean;

  @Input() binary: string;

  @Input() label: string;

  @Input() labelStyle: string = '';

  @Input() containerClass: string = '';

  @Output() onChange: EventEmitter<any> = new EventEmitter();

  model: any;

  onModelChange: Function = () => {};

  onModelTouched: Function = () => {};

  focused: boolean = false;

  checked: boolean = false;

  onClick(event,checkbox,focus:boolean) {
    event.preventDefault();

    if(this.disabled) {
      return;
    }

    this.checked = !this.checked;
    this.updateModel();

    if(focus) {
      checkbox.focus();
    }
    event.stopPropagation();
  }

  updateModel() {
    if(!this.binary) {
      if(this.checked)
        this.addValue(this.value);
      else
        this.removeValue(this.value);

      this.onModelChange(this.model);
    }
    else {
      this.onModelChange(this.checked);
    }

    this.onChange.emit(this.checked);
  }

  handleChange(event) {
    this.checked = event.target.checked;
    this.updateModel();
  }

  isChecked(): boolean {
    if(!this.binary)
      return this.findValueIndex(this.value) !== -1;
    else
      return this.model;
  }

  removeValue(value) {
    var index = this.findValueIndex(value);
    if(index >= 0) {
      this.model.splice(index, 1);
    }
  }

  addValue(value) {
    this.model.push(value);
  }

  onFocus(event) {
    this.focused = true;
  }

  onBlur(event) {
    this.focused = false;
    this.onModelTouched();
  }

  findValueIndex(value) {
    var index: number = -1;
    if(this.model) {
      for (var i = 0; i < this.model.length; i++) {
        if(this.model[i] == value) {
          index = i;
          break;
        }
      }
    }

    return index;
  }

  writeValue(model: any) : void {
    this.model = model;
    this.checked = this.isChecked();
  }

  registerOnChange(fn: Function): void {
    this.onModelChange = fn;
  }

  registerOnTouched(fn: Function): void {
    this.onModelTouched = fn;
  }

  setDisabledState(val: boolean): void {
    this.disabled = val;
  }
}

@NgModule({
  imports: [CommonModule],
  exports: [Checkbox],
  declarations: [Checkbox]
})
export class PibCheckboxModule { }




