import {
  Component, Input, Output, EventEmitter, ElementRef, ViewChild, OnDestroy, NgModule, style,
  animate, trigger, transition, state
} from "@angular/core";
import {CommonModule} from "@angular/common";

@Component({
  selector: "pib-modal",
  template: `
<div class="modal" 
     role="dialog"
     #modalRoot
     (keydown.esc)="closeOnEscape ? close() : 0"
     [ngClass]="{ 'in': _isOpened, 'fade': _isOpened }"
     [ngStyle]="{ 'display': _isOpened ? 'block' : 'none' }"
     (click)="closeOnOutsideClick ? close() : 0">
    <div [class]="'modal-dialog ' + modalClass" (click)="preventClosing($event)">
        <div  [@dialog] = "_isOpened" >
                <ng-content></ng-content>
        </div>
    </div>
</div>
`,
  animations: [
    trigger('dialog', [
      state('true' , style({ opacity: 1, transform: 'scale(1.0)' })),
      state('false', style({ opacity: 0, transform: 'scale(0.0)'  })),
      transition('1 => 0', animate('300ms')),
      transition('0 => 1', animate('300ms'))
    ])
  ]
})
export class Modal implements  OnDestroy{
  /**
   * css modal class
   * @type {string}
   */
  @Input('modalClass') modalClass: string = '';
  /**
   * close when click on esc leyboard
   * @type {boolean}
   */
  @Input('closeOnEscape') closeOnEscape: boolean = true;
  /**
   * close when click outside of modal content
   * @type {boolean}
   */
  @Input('closeOnOutsideClick') closeOnOutsideClick: boolean = true;
  /**
   * event emitted when modal is opened
   * @type {EventEmitter}
   */
  @Output('onOpen') onOpen = new EventEmitter(false);
  /**
   * event emitted when modal is closed
   * @type {EventEmitter}
   */
  @Output('onClose') onClose = new EventEmitter(false);
  /**
   * the modal root element
   * @type {ElementRef}
   */
  @ViewChild("modalRoot") modalRoot: ElementRef;
  /**
   * hold Modal state (opened, closed)
   * @type {boolean}
   * @private
   */
  public _isOpened = false;
  /**
   * the back drop html element
   * @type {HTMLElement}
   * @private
   */
  private _backdropElement: HTMLElement;

  /**
   * component constructor
   */
  constructor() {
    this.createBackDrop();
  }

  /**
   * onDestroy lifecycle hook
   */
  ngOnDestroy() {
    document.body.className = document.body.className.replace(/modal-open\b/, "");
    if (this._backdropElement && this._backdropElement.parentNode === document.body)
      document.body.removeChild(this._backdropElement);
  }

  /**
   * open the modal
   * @param args
   */
  open(...args: any[]) {
    if (this._isOpened)
      return;
    this._isOpened = true;
    this.onOpen.emit(args);
    document.body.appendChild(this._backdropElement);
    window.setTimeout(() => this.modalRoot.nativeElement.focus(), 0);
    document.body.className += " modal-open";
  }

  /**
   * close the modal
   * @param args
   */
  close(...args: any[]) {
    if (!this._isOpened)
      return;
    this._isOpened = false;
    this.onClose.emit(args);
    // document.body.removeChild(this._backdropElement);
    // document.body.className = document.body.className.replace(/modal-open\b/, "");
  }

  /**
   * prevent the modal from closing
   * @param event
   */
  public preventClosing(event: MouseEvent) {
    event.stopPropagation();
  }

  /**
   * get if the modal is opened
   * @returns {boolean}
   */
  isOpened():boolean{
    return this._isOpened;
  }

  /**
   * create the backdrop html element
   */
  private createBackDrop() {
    this._backdropElement = document.createElement("div");
    this._backdropElement.classList.add("modal-backdrop");
    this._backdropElement.classList.add("fade");
    this._backdropElement.classList.add("in");
  }

}

@NgModule({
  imports: [CommonModule],
  exports: [Modal],
  declarations: [Modal]
})
export class PibModalModule { }



