import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Rx';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
@Injectable()
export class CountriesDataService {
  private __data: any[] = [];
  private __data_loaded = false
  localeFR = {
    firstDayOfWeek: 0,
    dayNames: ["Dimanche", "Lundi", "Mardi", "Mercredi", "Jeudi", "Vendredi", "Samedi"],
    dayNamesShort: ["Dim", "Lun", "Mar", "Mer", "Jeu", "Ven", "Sam"],
    dayNamesMin: ["Di", "Lu", "Ma", "Me", "Je", "Ve", "Sa"],
    monthNames: ["Janvier", "Fvrier", "Mars", "Avril", "Mai", "Juin", "Juillet", "Aot", "Septembre", "Octobre", "Novembre", "Dcembre"],
    monthNamesShort: ["janv.", "fvr.", "mars", "avril", "mai", "juin", "juil.", "aot", "sept.", "oct.", "nov.", "dc."]
  }
  constructor(private http: Http) {
  }
  getData() {
    if (this.__data_loaded) {
      return new Promise<any[]>((resolve, reject) => {
        this.resolvePromise(resolve)
      })
    } else {
      return new Promise<any[]>((resolve, reject) => {
        this.http.get("./assets/data/countries.json")
          .subscribe((data) => {
            this.__data = data.json()
            this.__data_loaded = true
            this.resolvePromise(resolve)
          });
      })
    }
  }
  getCountries() {
    if (this.__data_loaded) {
      return new Promise<any[]>((resolve, reject) => {
        this.getCountriesData(resolve)
      })
    } else {
      return new Promise<any[]>((resolve, reject) => {
        this.http.get("./assets/data/countries.json")
          .subscribe((data) => {
            this.__data = data.json()
            this.__data_loaded = true
            this.getCountriesData(resolve)
          });
      })
    }
  }
  getCountriesData(resolve: Function) {
    let data = [];
    let priv = ['FR', 'CH', 'DE', 'LU', 'US', 'IT', 'ES', 'UK']
    for (let i = 0; i < this.__data.length; i++) {
      //if (priv.indexOf(this.__data[i].code) > -1) {
          data.push({
            label: this.__data[i].name, value: this.__data[i].code
          })
      //}
    }
    resolve(data);
  }
  resolvePromise(resolve: Function) {
    let data = [];
    let priv = ['FR', 'CH', 'DE', 'LU', 'US', 'IT', 'ES', 'UK']
    for (let i = 0; i < this.__data.length; i++) {
      //if (priv.indexOf(this.__data[i].code) > -1) {
        data.push({
          label: this.__data[i].code, value: this.__data[i].dial_code
        })
      //}
    }
    resolve(data);
  }
  getLocale(lang) {
    if (lang == 'fr') {
      return this.localeFR
    } else {
      return null
    }
  }
}



