import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AccountTypeComponent } from '../main/account-type/account-type.component';
import { PersonalInformationsComponent } from '../main/personal-information/personal-informations.component';
import {PersonalInformationSecondPageComponent} from '../main/personal-information-second-page/personal-information-second-page.component'
import {ContactDetailsComponent} from '../main/contact-details/contact-details.component';
import {ProfessionalSituationComponent} from '../main/professional-situation/professional-situation.component';
import {MonthlyIncomeComponent} from '../main/monthly-income/monthly-income.component';
import {FiscalInformationComponent} from '../main/fiscal-information/fiscal-information.component';
import {PatrimonyComponent} from '../main/patrimony/patrimony.component';
import {FollowAccountPrefComponent} from '../main/follow-account-pref/follow-account-pref.component';
import {YourAccountComponent} from '../main/your-account/your-account.component';
import {CreditCardComponent} from '../main/credit-card/credit-card.component';
import {CreditCardSecondPageComponent} from '../main/credit-card-second-page/credit-card-second-page.component';
import {RecapValidationComponent} from '../main/recap-validation/recap-validation.component';
import {OpeningAccountInformationComponent} from '../main/opening-account-information/opening-account-information.component';
import {ESignComponent} from '../main/e-sign/e-sign.component';
import {SavedESignComponent} from "../main/saved-e-sign/saved-e-sign.component";
import {SharedModelService} from '../services/shared-model.service';

import {UploadProofIdentityComponent} from '../main/upload-proof-identity/upload-proof-identity.component';
import {UploadProofHandwrittenSignatureComponent} from '../main/upload-proof-handwritten-signature/upload-proof-handwritten-signature.component';
import {UploadProofTaxNoticeComponent} from '../main/upload-proof-tax-notice/upload-proof-tax-notice.component';
import {ProofUploadValidationComponent} from '../main/proof-upload-validation/proof-upload-validation.component';
import {AccountOpeningValidationComponent} from '../main/account-opening-validation/account-opening-validation.component';

// Guards import
import {PersonalInformationGuard} from '../services/guards/personal-information-guard'
import {PersonalInformationSecondPageGuard} from '../services/guards/personal-information-second-page-guard'

const routes: Routes = [
    
    {
        path : '',
        redirectTo:'account-type',
        pathMatch:'full'
    },
    {
        path:'account-type',
        component:AccountTypeComponent,
    },
    {
        path:'personal-information',
        component:PersonalInformationsComponent,
        canActivate: [PersonalInformationGuard]
    },
    {
        path:'personal-information/:internalStep',
        component:PersonalInformationsComponent,
        canActivate: [PersonalInformationGuard]
    },
    {
        path:'personal-information-second-page',
        component:PersonalInformationSecondPageComponent,
        canActivate: [PersonalInformationSecondPageGuard]
    },
    {
        path:'personal-information-second-page/:internalStep',
        component:PersonalInformationSecondPageComponent,
        canActivate: [PersonalInformationSecondPageGuard]
    },
    {
        path:'coordonees',
        component:ContactDetailsComponent
    },
    {
        path:'professional-situation',
        component:ProfessionalSituationComponent
    },
    {
        path:'monthly-income',
        component:MonthlyIncomeComponent
    },
    {
        path:'fiscal-information',
        component:FiscalInformationComponent
    },
    {
        path:'patrimony',
        component:PatrimonyComponent
    },
    {
        path:'follow-account',
        component:FollowAccountPrefComponent
    },
    {
        path:'your-account',
        component:YourAccountComponent
    },
    {
        path:'card-type',
        component:CreditCardComponent
    },{
        path:'card-utilisation',
        component:CreditCardSecondPageComponent
    },
    {
        path:'recap-validation',
        component:RecapValidationComponent
    },{
        path:'opening-account-information',
        component:OpeningAccountInformationComponent
    },
    {
        path:'e-sign',
        component:ESignComponent
    },{
        path:'saved-e-sign',
        component:SavedESignComponent
    },
    {
        path:'upload-identity-proof',
        component:UploadProofIdentityComponent
    },
    {
        path:'upload-signature',
        component:UploadProofHandwrittenSignatureComponent
    },
    {
        path:'upload-tax-notice',
        component:UploadProofTaxNoticeComponent
    },
    {
        path:'proof-upload-validation',
        component:ProofUploadValidationComponent
    },
    {
        path:'account-opening-validation',
        component:AccountOpeningValidationComponent
    }

];

@NgModule({
    imports: [RouterModule.forRoot(routes, {useHash: true})],
    exports: [RouterModule],
    providers: [PersonalInformationGuard, PersonalInformationSecondPageGuard]
})
export class CreateAccountRoutingModule { }



