import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
@Injectable()
export class FillDataStepsChangerService {
  private _steps : BehaviorSubject<FILL_DATA_STEPS>;
  private __currentStep = FILL_DATA_STEPS.INFORMATION;
  constructor() { 
     this._steps = new BehaviorSubject(this.__currentStep);
  }
  getCurrentStep(){
    return this._steps.asObservable();
  }
  setCurrentStep(step:FILL_DATA_STEPS){
    this.__currentStep = step;
    this._steps.next(step);
  }
}
export enum FILL_DATA_STEPS{
  INFORMATION = 0,
  SITUATION,
  ACCOUNT,
  VALIDATION
}


