import { Component, HostBinding, OnInit } from '@angular/core';
import {Router}  from '@angular/router';
import {Location} from '@angular/common';
import {TranslateService} from 'ng2-translate';
import { FillDataStepsChangerService, FILL_DATA_STEPS } from '../../services/fill-data-steps-changer.service';
import {HeaderStateService,HEADER_STATE} from '../../services/header-state.service';
import { ScrollService } from '../../services/scroll.service';
import { RouterStateService } from '../../services/router-state.service';
import { routerTransition, holderTransition } from '../../routing/router.animations';
@Component({
  selector: 'app-monthly-income',
  templateUrl: './monthly-income.component.html',
  styleUrls: ['./monthly-income.component.css'],
  animations: [routerTransition(), holderTransition()],
  host: {'[@routerTransition]': ''},
})
export class MonthlyIncomeComponent implements OnInit {

/**
 * @var {boolean} anotherSourceOfIncomeAdded
 */
anotherSourceOfIncomeAdded : boolean = false;
monthlyNetSalary : string;
BICBNCMonthly : number;
pensionsMonthlyRetirement : number;
allowancesMonthly : number;
otherMonthlyIncome : number;
@HostBinding('@routerTransition') routerAnimation;


  /**
   * @constructor
   * Constructor of MonthlyIncomeComponent class
   * @param {TranslateService} translate
   * @param {Location} _location location service
   * @param {Router} router router service
   * @param {HeaderStateService} _headerSteps
   * @param {FillDataStepsChangerService} _fillDataStepService 
   */
  constructor(translate: TranslateService,private router: Router,private _location:Location, 
              private _headerSteps:HeaderStateService,
              private _fillDataStepService: FillDataStepsChangerService,
              private _routerState: RouterStateService,
              private _scroll: ScrollService) { 
    this.routerAnimation = this._routerState.lastAction;
    setTimeout(() => {
      this.routerAnimation = 'next'
    }, 500);
  }

/**
 * Initialisations
 * @function ngOnInit
 */
  ngOnInit() {
    this._fillDataStepService.setCurrentStep(FILL_DATA_STEPS.SITUATION);
    this._headerSteps.setCurrentState(HEADER_STATE.FILL_DATA);
  }

  onMonthlyNetSalaryChange(event) {
    if(event.value === undefined)
      console.log("undefined")
    else if(event.value.slice(-1) != '$') {
      
      this.monthlyNetSalary = event.value;
      this.monthlyNetSalary = this.monthlyNetSalary.replace(/\D/g,'');
      this.monthlyNetSalary = this.monthlyNetSalary.concat("$")
      console.log(this.monthlyNetSalary);
    }
  }


  /**
   * Add another source of income
   * @function addAnotherSourceOfIncome
   */
  addAnotherSourceOfIncome() {
    this.anotherSourceOfIncomeAdded = true;
  }

  /**
   * Cancel the other source of income
   * @function Cancel
   */
  Cancel() {
    this.anotherSourceOfIncomeAdded = false;
  }

  /**
   * Move to the previous step
   * @function goBack
   */
  goBack(){
    this._location.back();
  }

  /**
   * Move to the next step
   * @function next
   */
  next(){
    this.routerAnimation = 'prev';
    setTimeout(() => {
      this.router.navigate(['fiscal-information']);
    }, 50);
  }

}



