import { AppComponent } from './app.component';
import { HeaderComponent } from './header/header.component';
import { FooterComponent } from './footer/footer.component';
import { MainComponent } from './main/main.component';
import {HeaderStateService} from './services/header-state.service';
import {FillDataStepsChangerService} from './services/fill-data-steps-changer.service';
import {SharedModelService} from './services/shared-model.service';
import {LocalDataService} from './services/local-data.service';
import {RouterStateService} from './services/router-state.service';
import {ScrollService} from './services/scroll.service';
import {TranslateStaticLoader, TranslateLoader, TranslateModule, TranslateService} from "ng2-translate";
import { AccountTypeComponent } from './main/account-type/account-type.component';
import { PersonalInformationsComponent } from './main/personal-information/personal-informations.component';
import { PibTabMenuModule } from'./components/tab.component'
import { PibTextFieldModule } from './components/text-field.component'
import {PibTextAreaModule} from './components/text-area.component';
import { PibSideBarModule } from './components/sidebar.component';
import {PibDropdownModule} from "./components";
import {PibSegmentControlModule} from "./components";
import {PibCalendarModule} from './components'
import {PibCheckboxModule} from './components/checkbox.component';
import { PersonalInformationSecondPageComponent } from './main/personal-information-second-page/personal-information-second-page.component';
import {PibRadioButtonModule} from './components';
import { ContactDetailsComponent } from './main/contact-details/contact-details.component';
import { MonthlyIncomeComponent } from './main/monthly-income/monthly-income.component';
import { PatrimonyComponent } from './main/patrimony/patrimony.component';
import { ProfessionalSituationComponent } from './main/professional-situation/professional-situation.component';
import { FiscalInformationComponent } from './main/fiscal-information/fiscal-information.component';
import { FollowAccountPrefComponent } from './main/follow-account-pref/follow-account-pref.component';
import { YourAccountComponent } from './main/your-account/your-account.component';
import { CreditCardComponent } from './main/credit-card/credit-card.component';
import { CreditCardSecondPageComponent } from './main/credit-card-second-page/credit-card-second-page.component';
import { RecapValidationComponent } from './main/recap-validation/recap-validation.component';
import { OpeningAccountInformationComponent } from './main/opening-account-information/opening-account-information.component';
import { ESignComponent } from './main/e-sign/e-sign.component';
import { SavedESignComponent } from './main/saved-e-sign/saved-e-sign.component';
import { UploadProofIdentityComponent } from './main/upload-proof-identity/upload-proof-identity.component';
import { UploadProofHandwrittenSignatureComponent } from './main/upload-proof-handwritten-signature/upload-proof-handwritten-signature.component';
import { UploadProofTaxNoticeComponent } from './main/upload-proof-tax-notice/upload-proof-tax-notice.component';
import { ProofUploadValidationComponent } from './main/proof-upload-validation/proof-upload-validation.component';
import { AccountOpeningValidationComponent } from './main/account-opening-validation/account-opening-validation.component';
import { PibFileUploadModule } from './components/file-upload.component';
import { PibDropDownEditableModule } from './components/drop-down-editable.component';
import { PibModalModule } from './components/modal.component';
import { HsbcTimePipe } from './pipes/hsbc-time.pipe';
import {CountriesDataService} from './services/countries-data.service';
import {UsefulDataService} from './services/useful-data.service';

// // Guards import
// import {PersonalInformationGuard} from './services/guards/personal-information-guard'

export function createTranslateLoader(http: Http) {
  return new TranslateStaticLoader(http, './assets/i18n', '.json');
}
@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    FooterComponent,
    MainComponent,
    AccountTypeComponent,
    PersonalInformationsComponent,
    PersonalInformationSecondPageComponent,
    ContactDetailsComponent,
    MonthlyIncomeComponent,
    PatrimonyComponent,
    ProfessionalSituationComponent,
    FiscalInformationComponent,
    FollowAccountPrefComponent,
    YourAccountComponent,
    CreditCardComponent,
    CreditCardSecondPageComponent,
    RecapValidationComponent,
    OpeningAccountInformationComponent,
    ESignComponent,
    SavedESignComponent,
    UploadProofIdentityComponent,
    UploadProofHandwrittenSignatureComponent,
    UploadProofTaxNoticeComponent,
    ProofUploadValidationComponent,
    AccountOpeningValidationComponent,
    HsbcTimePipe
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    PibTabMenuModule,
    PibTextFieldModule,
    PibCalendarModule,
    PibCheckboxModule,
    PibSideBarModule,
    PibRadioButtonModule,
    CreateAccountRoutingModule,
    PibStepsModule,
    PibSegmentControlModule,
    PibDropdownModule,
    PibCheckboxModule,
    PibFileUploadModule,
    PibDropDownEditableModule,
    PibModalModule,
    PibTextAreaModule,
    TranslateModule.forRoot({
      provide: TranslateLoader,
      useFactory: (createTranslateLoader),
      deps: [Http]
    }),
  ],
  providers: [HeaderStateService,FillDataStepsChangerService,SharedModelService,CountriesDataService,UsefulDataService,LocalDataService,RouterStateService,ScrollService],
  bootstrap: [AppComponent]
})
export class AppModule { 
  constructor(private translate : TranslateService, private localData: LocalDataService){
    translate.addLangs(['fr', 'en']);
    translate.setDefaultLang('fr');
    if(localData.getDataLang()){
      translate.use(localData.getDataLang())
    }
    else {
      let browserLang = translate.getBrowserLang();
      translate.use(browserLang.match(/en|fr/) ? browserLang : 'fr');
    }
  }
}



