import {NgModule,Component,Input,Output,AfterViewInit,ElementRef,EventEmitter,forwardRef,ViewChild} from '@angular/core';
import {CommonModule} from '@angular/common';
import {NG_VALUE_ACCESSOR, ControlValueAccessor} from '@angular/forms';

export const RADIO_VALUE_ACCESSOR: any = {
  provide: NG_VALUE_ACCESSOR,
  useExisting: forwardRef(() => RadioButton),
  multi: true
};

@Component({
  selector: 'pib-radioButton',
  template: `
             <div>
               <input type="radio" #rb name="radio" [attr.name]="name" [attr.value]="value" [checked]="checked" (change)="onChange($event)" (focus)="onFocus($event)" (blur)="onBlur($event)">
               <label (click)="select()" *ngIf="label" [class] = "labelClass" [ngStyle] = "labelStyle"><span><i></i></span>{{label}}</label>
             </div>
            `,
  providers: [RADIO_VALUE_ACCESSOR]
})
export class RadioButton implements ControlValueAccessor,AfterViewInit {

  @Input() value: any;

  @Input() name: string;

  @Input() disabled: boolean;

  @Input() label: string;

  @Input() labelStyle: string = '';

  @Input() labelClass: string = '';

  @Output() onClick: EventEmitter<any> = new EventEmitter();

  @ViewChild('rb') inputViewChild: ElementRef;

  public input: HTMLInputElement;

  public onModelChange: Function = () => {};

  public onModelTouched: Function = () => {};

  public checked: boolean;

  public focused: boolean;

  ngAfterViewInit() {
    this.input = <HTMLInputElement> this.inputViewChild.nativeElement;
  }

  handleClick() {
    if(!this.disabled) {
      this.onClick.emit(null);
      this.select();
    }
  }

  select() {
    if(!this.disabled) {
      this.input.checked = true;
      this.checked = true;
      this.onModelChange(this.value);
      this.onClick.emit(null);
    }
  }

  writeValue(value: any) : void {
    this.checked = (value == this.value);

    if(this.input) {
      this.input.checked = this.checked;
    }
  }

  registerOnChange(fn: Function): void {
    this.onModelChange = fn;
  }

  registerOnTouched(fn: Function): void {
    this.onModelTouched = fn;
  }

  setDisabledState(val: boolean): void {
    this.disabled = val;
  }

  onFocus(event) {
    this.focused = true;
  }

  onBlur(event) {
    this.focused = false;
    this.onModelTouched();
  }

  onChange(event) {
    this.select();
  }
}

@NgModule({
  imports: [CommonModule],
  exports: [RadioButton],
  declarations: [RadioButton]
})
export class PibRadioButtonModule { }



