import {Component, OnInit, OnDestroy, Input, NgModule, Output, EventEmitter} from '@angular/core';
import {TimerObservable} from "rxjs/observable/TimerObservable";
import {Subscription} from "rxjs";
import {CommonModule} from "@angular/common";
import {ProgressBar,PibBrogressBarModule} from "./progress-bar.component";

@Component({
  selector: 'pib-timer',
  template : `
             <pib-progress-bar [progress]="progressWidth"></pib-progress-bar>
            `
})
export class TimerComponent implements OnInit,OnDestroy {
  /**
   * the count down duration
   * @type {number}
   */
  @Input('countDownDuration') max = 30 * 60;
  /**
   * repeat the countdown from the beginning when reach 0
   * @type {boolean}
   */
  @Input('repeat') repeat = true;
  /**
   * emit onReach event when timer is equal to emitWhen
   * @type {number}
   */
  @Input('emitWhen') emitWhen = 15;
  /**
   * start from a count down if not set or 0, start from max
   * @type {number}
   */
  @Input('startFrom') start:number;
  /**
   * event emitted when emitWhen or 0 are reached
   * @type {EventEmitter}
   */
  @Output('onReach') onReach: EventEmitter<any> = new EventEmitter();
  /**
   * emit a count down event {second,tick}
   * @type {EventEmitter}
   */
  @Output('onCountDown') onCountDown: EventEmitter<any> = new EventEmitter();

  //The count down subscription reference
  private subscription : Subscription;
  // count down string
  tick = "03:00";
  //progress bar width
  progressWidth = 0;
  constructor() {
  }
  //onInit lifecycle hook
  //Initialize the countdown
  ngOnInit() {
    this.initTimer();
  }
  //Update the timer, called each second
  updateTimer(){
    //update the progress bar width and the count down string
    this.getTimeFromStart();
    // create a timer observable to start  count down
    // run every 1 second with a start delay 2 seconds
    let timer = TimerObservable.create(2000, 1000);
    if(this.subscription){
      this.subscription.unsubscribe()
    }
    this.subscription = timer.subscribe(this.onTimer);
  }
  //Initialise the timer to 0
  initTimer(){
    if(!this.start){
      this.start = this.max;
    }
    this.updateTimer();
  }
  onTimer(){
      this.start = this.start - 1;
      this.getTimeFromStart();
      if(this.emitWhen === this.start) {
        this.onReach.emit({secondReached: this.emitWhen});
      }
      if(this.start <= 0){
        this.subscription.unsubscribe();
        this.onReach.emit({secondReached:0});
        if(this.repeat){
          this.initTimer();
        }
      }
    }
  /**
   * edit tick string to update the html representation of the count down
   */
  getTimeFromStart(){
    let m = Math.floor(this.start/60);
    let s = this.start % 60;
    let ms = m.toString();
    let ss = s.toString();
    if(s < 10){
      ss = '0'+ss;
    }
    if(m < 10){
      ms = '0'+ms;
    }
    let t = this.max - this.start;
    // update progress bar width
    this.progressWidth = 100 - ((100 * t) / this.max);
    // update count down string
    this.tick = ms + ':'+ss;
    this.onCountDown.emit({second:this.start,tick:this.tick});
  }
  //On destroy lifecycle event, cleaning the subscription
  ngOnDestroy(){
    if(this.subscription){
      this.subscription.unsubscribe();
      this.subscription = null;
    }
  }
}
@NgModule({
  imports: [CommonModule,PibBrogressBarModule],
  exports: [TimerComponent],
  declarations: [TimerComponent]
})
export class PibTimerModule { }




